// Caminho: src/components/Footer.jsx

// =================================================================================================
// ARQUIVO: Footer.jsx
// PROPSITO: Este arquivo define o componente React chamado 'Footer'.
//            Um "componente"  uma pea fundamental na construo de interfaces com React.
//            Pense nele como um bloco de Lego: voc cria vrios blocos (componentes)
//            e depois os junta para montar sua pgina ou aplicao.
//
//            O componente 'Footer' (Rodap)  a seo que geralmente aparece na
//            parte inferior de todas as pginas de um site. Ele costuma conter
//            informaes como direitos autorais (copyright), crditos do desenvolvedor,
//            links para termos de servio, poltica de privacidade, ou outras informaes
//            relevantes que no fazem parte do contedo principal da pgina.
//
//            Este Footer especfico  um exemplo de um componente "esttico" e "presentacional":
//            - Esttico: Seu contedo no muda com base em interaes do usurio ou
//                        outros dados dinmicos da aplicao (exceto o ano atual, que 
//                        calculado uma vez quando o componente  renderizado).
//            - Presentacional: Sua principal funo  exibir informaes e estrutura visual,
//                              sem muita lgica complexa de programao.
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS ---

// Importamos a biblioteca 'React'.
// Esta linha  crucial porque:
//  a) Permite que usemos JSX (JavaScript XML) neste arquivo. JSX  a sintaxe
//     que se parece com HTML (ex: <p>, <div>) e que usamos para descrever
//     a estrutura da interface do nosso componente.
//  b) O JSX  transformado (compilado) pelo Babel (uma ferramenta que faz parte
//     do setup do Create React App) em chamadas de funo do tipo `React.createElement(...)`.
//     Portanto, 'React' precisa estar no escopo para que essas chamadas funcionem.
// Em verses muito recentes do React (17+) com novas transformaes JSX, essa importao
// explcita pode no ser estritamente necessria em TODOS os arquivos que usam JSX,
// mas  uma boa prtica mant-la, especialmente para fins de aprendizado e para garantir
// compatibilidade com diferentes configuraes de build.
import React from "react";

// Importamos o objeto 'styles' do nosso arquivo CSS Module correspondente, 'Footer.module.css'.
// - 'styles': Este  um nome de varivel que escolhemos (uma conveno comum).
//             Ele se tornar um objeto JavaScript.
// - './Footer.module.css': O './' indica que o arquivo CSS est localizado na MESMA PASTA
//                          que este arquivo Footer.jsx.
//                          A extenso '.module.css'  ESPECIAL. Ela diz ao sistema de build
//                          para processar este arquivo CSS como um "CSS Module".
// - CSS Modules: A principal vantagem  o ESCOPO LOCAL das classes CSS.
//                Isso significa que se voc definir uma classe como '.meuEstilo'
//                em Footer.module.css, ela no entrar em conflito com uma classe
//                '.meuEstilo' definida em outro arquivo CSS de outro componente.
//                O sistema de build renomeia as classes para serem nicas globalmente
//                (ex: '.Footer_meuEstilo__AbCdE'). No nosso cdigo JSX, acessaremos
//                essa classe atravs do objeto 'styles', como `styles.meuEstilo`.
import styles from "./Footer.module.css";

// --- 2. DEFINIO DO COMPONENTE FUNCIONAL 'Footer' ---

// 'Footer'  definido como um "componente funcional".
// Em React, um componente funcional , em sua forma mais bsica, uma funo JavaScript
// que retorna elementos React (geralmente escritos em JSX) que descrevem o que
// deve ser renderizado (mostrado) na tela.
//
// Esta funo no recebe nenhum argumento 'props' (propriedades) porque, neste caso,
// o contedo do rodap  fixo e no depende de dados passados pelo componente pai.
// Se quisssemos, por exemplo, que o nome da empresa no rodap viesse de fora,
// poderamos definir o componente como: const Footer = (props) => { ... }
// e acessar props.nomeDaEmpresa.
const Footer = () => {
  // --- A. LGICA DO COMPONENTE (JavaScript que roda ANTES do JSX ser retornado) ---

  // Para exibir o ano atual dinamicamente na mensagem de direitos autorais,
  // usamos o objeto 'Date' embutido no JavaScript.
  // 1. `new Date()`: Cria um novo objeto do tipo 'Date'. Quando chamado sem argumentos,
  //                  este objeto representa a data e hora EXATAS do momento em que
  //                  esta linha de cdigo  executada.
  const dataHoraAtuais = new Date();

  // 2. `.getFullYear()`: Este  um mtodo (uma funo que pertence a um objeto)
  //                      do objeto 'Date'. Ele retorna o ano da data com quatro dgitos
  //                      (ex: 2023, 2024, 2025).
  const anoCorrente = dataHoraAtuais.getFullYear();

  // No h mais lgica complexa neste componente, pois ele  principalmente para exibio.

  // --- B. RETORNO DO JSX (O QUE O COMPONENTE VAI MOSTRAR NA TELA) ---
  // A instruo 'return (...);' especifica a estrutura da interface do usurio
  // que este componente 'Footer' ir gerar. O cdigo dentro dos parnteses  JSX.
  // JSX nos permite escrever uma estrutura parecida com HTML diretamente dentro do nosso JavaScript.

  return (
    // A tag `<footer />`  uma tag HTML5 semntica.
    // Usar tags HTML semnticas (como <header>, <nav>, <main>, <article>, <aside>, <footer>)
    //  uma boa prtica porque ajuda:
    //  - Acessibilidade: Leitores de tela podem entender melhor a estrutura da pgina.
    //  - SEO (Search Engine Optimization): Mecanismos de busca podem indexar melhor o contedo.
    //  - Manuteno do cdigo: Torna o cdigo mais legvel para outros desenvolvedores (e para voc no futuro).
    //
    // `className={styles.footer}`:
    //   - `className`: Em JSX, usamos `className` em vez do atributo `class` do HTML puro.
    //                  Isso ocorre porque `class`  uma palavra reservada em JavaScript (usada para criar classes).
    //   - `{styles.footer}`: Aqui estamos acessando a propriedade `footer` do objeto `styles`
    //                        que importamos do nosso arquivo `Footer.module.css`.
    //                        Se em `Footer.module.css` tivermos `.footer { ... }`,
    //                        `styles.footer` conter uma string com o nome da classe CSS
    //                        transformado e nico (ex: "Footer_footer__u7X5r").
    //                        Isso aplica os estilos definidos para `.footer` no CSS Module
    //                        a este elemento `<footer>`.
    <footer className={styles.footer}>
      {/*
        Pargrafo para a mensagem de direitos autorais.
        - `&copy;`:  uma "entidade HTML" que representa o smbolo de copyright ().
                    Entidades HTML so usadas para exibir caracteres especiais que podem ter
                    outro significado em HTML/XML ou que no so fceis de digitar.
        - `{anoCorrente}`: Usamos chaves `{}` para "escapar" do JSX e embutir uma expresso
                           JavaScript. Aqui, estamos inserindo o valor da varivel `anoCorrente`
                           (que calculamos acima para ser o ano atual) diretamente no texto.
        - ``: Emojis so caracteres Unicode e podem ser usados diretamente no JSX/HTML.
      */}
      <p>
        &copy; {anoCorrente} ZooDex. Feito com amor e JavaScript  no
        Unilavras.
      </p>
      <p>
        Um projeto educacional inspirado nos tazos clssicos e na importncia da
        conscientizao sobre a fauna.
      </p>

      {/*
        Uma `<div>` (diviso)  usada aqui para agrupar a seo de "disclaimer"
        (avisos legais ou atribuies). Isso ajuda a aplicar estilos especficos
        a todo este bloco de informaes.
        Aplicamos a classe `styles.disclaimer` a esta div.
      */}
      <div className={styles.disclaimer}>
        <p>
          Imagens dos Tazos "Coleo Animais em Extino - 1998" &copy;
          Chicletes Ping Pong.
        </p>
        <p>
          Projeto com fins educacionais e no comerciais, focado na
          conscientizao sobre a fauna.
        </p>
        {/*
          Poderamos adicionar mais informaes aqui, como:
          - Links para as fontes dos dados dos animais.
          - Nomes dos desenvolvedores/alunos envolvidos.
          - Um link para o repositrio do projeto no GitHub (se for pblico).
          Exemplo de link:
          <p>
            Veja o cdigo no <a href="URL_DO_SEU_REPOSITORIO_GITHUB" target="_blank" rel="noopener noreferrer">GitHub</a>.
          </p>
          - 'target="_blank"': Faz o link abrir em uma nova aba/janela do navegador.
          - 'rel="noopener noreferrer"': So atributos de segurana importantes para links `target="_blank"`:
            - 'noopener': Previne que a nova pgina tenha acesso ao objeto `window` da pgina original
                          (uma medida de segurana contra phishing).
            - 'noreferrer': Previne que informaes de referncia (de onde o usurio veio)
                            sejam passadas para a nova pgina.
        */}
      </div>
    </footer> // Fim da tag <footer>
  ); // Fim da instruo return
}; // Fim da definio do componente funcional Footer

// --- 3. EXPORTAO DO COMPONENTE ---

// `export default Footer;`
// Esta linha torna o componente `Footer` disponvel para ser importado e utilizado
// em outros arquivos/componentes da nossa aplicao (principalmente no `App.js`).
// - `export`: Palavra-chave do JavaScript (ES6 Modules) para exportar funcionalidades.
// - `default`: Indica que `Footer`  a exportao principal (padro) deste arquivo.
//              Isso significa que, ao importar em outro arquivo, podemos dar qualquer nome
//               varivel que recebe o componente, embora seja conveno usar o mesmo nome.
//              Exemplo de importao no App.js:
//                `import RodapeCustomizado from './components/Footer';`
//                `// ... e depois usar <RodapeCustomizado /> no JSX do App.js`
//              Mas o mais comum :
//                `import Footer from './components/Footer';`
export default Footer;
