// Caminho: src/components/MapComponent.jsx

// =================================================================================================
// ARQUIVO: MapComponent.jsx
// PROPSITO: Este componente  responsvel por exibir um mapa interativo mostrando
//            a distribuio geogrfica de um animal, com marcadores nos pases nativos.
//            Ele utiliza a biblioteca 'React-Leaflet', que  uma interface React
//            para a popular biblioteca de mapas 'Leaflet.js'.
//
// CONCEITOS PRINCIPAIS DO REACT E BIBLIOTECAS EXTERNAS UTILIZADOS AQUI:
//  - Componentes Funcionais: A estrutura do nosso componente.
//  - Props: Recebe o objeto 'animal' do AnimalDetail.jsx para saber quais pases exibir.
//  - useState: Para controlar qual pas est destacado (quando o mouse passa sobre ele na lista).
//  - useEffect: Para lgica que precisa ser executada aps a renderizao ou quando
//               certas props/estados mudam (ex: verificar a API key, ajustar a viso do mapa).
//  - useRef: Para obter referncias diretas a elementos do mapa (os marcadores),
//            embora no cdigo original essa funcionalidade estivesse parcialmente comentada.
//  - React-Leaflet: Uma biblioteca externa. Aprenderemos a importar e usar seus componentes
//                   como <MapContainer>, <TileLayer>, <Marker>, <Popup>, e o hook 'useMap'.
//  - Leaflet.js (implcito): A biblioteca JavaScript subjacente que o React-Leaflet utiliza.
//  - Variveis de Ambiente: Como o cdigo tenta acessar uma API Key para o servio de mapas.
//  - Manipulao de Dados: Processar a lista de pases nativos e buscar suas coordenadas.
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS E FUNES ---

// Importamos React e os Hooks 'useEffect', 'useState', e 'useRef'.
import React, { useEffect, useState, useRef } from "react";

// Importamos os componentes e hooks da biblioteca React-Leaflet.
// - MapContainer: O componente principal que cria o container do mapa.
// - TileLayer: Define a camada de "azulejos" (as imagens que formam o mapa base).
// - Marker: Usado para colocar um marcador (pino) em uma localizao especfica.
// - Popup: Uma pequena caixa de informao que pode aparecer ao clicar em um marcador.
// - useMap: Um hook do React-Leaflet que d acesso direto  instncia do mapa Leaflet subjacente,
//           permitindo chamar mtodos da API do Leaflet (como setView, fitBounds).
import { MapContainer, TileLayer, Marker, Popup, useMap } from "react-leaflet";

// Importamos o arquivo CSS principal do Leaflet.
// Sem ele, o mapa no ser estilizado corretamente (controles podem sumir, etc.).
//  crucial garantir que este CSS seja carregado (geralmente no index.html ou importado aqui/App.js).
import "leaflet/dist/leaflet.css";

// Importamos a biblioteca Leaflet diretamente (L).
// Isso  necessrio para algumas customizaes, como criar cones personalizados.
import L from "leaflet";

// Importamos nossa funo utilitria 'get' para acessar dados do animal de forma segura.
import { get } from "../data/animalData"; // Certifique-se que o caminho est correto.

// Importamos o objeto 'countryCoordinates' que mapeia nomes de pases para suas coordenadas [latitude, longitude].
import { countryCoordinates } from "../data/countryCoordinates"; // Certifique-se que o caminho est correto.

// Importamos os estilos CSS Module para este componente.
import styles from "./MapComponent.module.css";

// --- 2. CONFIGURAO DE CONES PERSONALIZADOS PARA MARCADORES (Leaflet) ---
// O Leaflet usa cones para os marcadores. Podemos usar os padres ou criar os nossos.
// Aqui, definimos dois cones: um padro e um para quando um pas est "destacado".

// cone Padro do Marcador (azul, fornecido pelo Leaflet)
const defaultIcon = new L.Icon({
  iconUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon.png", // URL da imagem do cone.
  iconRetinaUrl:
    "https://unpkg.com/leaflet@1.9.4/dist/images/marker-icon-2x.png", // URL para telas de alta resoluo.
  shadowUrl: "https://unpkg.com/leaflet@1.9.4/dist/images/marker-shadow.png", // URL da sombra do cone.
  iconSize: [25, 41], // Tamanho do cone em pixels [largura, altura].
  iconAnchor: [12, 41], // Ponto do cone que corresponder  coordenada do marcador (ponta inferior do pino).
  popupAnchor: [1, -34], // Ponto a partir do qual o popup deve abrir, relativo ao iconAnchor.
  shadowSize: [41, 41], // Tamanho da sombra.
});

// cone para Marcador Destacado (vermelho)
// Usamos um cone vermelho de um repositrio pblico para diferenciar.
const highlightedIcon = new L.Icon({
  iconUrl:
    "https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-red.png",
  shadowUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png",
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
});

// --- 3. COMPONENTE AUXILIAR INTERNO: ChangeView ---
// Este componente  um "truque" comum com React-Leaflet para controlar programaticamente
// a viso do mapa (centro, zoom, ou ajustar aos limites dos marcadores)
// em resposta a mudanas nas props.
// O <MapContainer> s define a viso inicial. Para mud-la dinamicamente, precisamos de algo assim.
//
// Props que este componente espera:
//  - center: Um array [latitude, longitude] para centralizar o mapa.
//  - zoom: Um nmero para o nvel de zoom.
//  - bounds: Um objeto L.LatLngBounds (do Leaflet) que define uma rea retangular.
//            Se fornecido, o mapa tentar se ajustar para mostrar todos os pontos dentro desses limites.
const ChangeView = ({ center, zoom, bounds }) => {
  // O hook 'useMap()' nos d acesso  instncia do mapa Leaflet.
  const map = useMap();

  // Usamos 'useEffect' para aplicar as mudanas na viso do mapa.
  // Este efeito ser executado sempre que 'center', 'zoom', 'bounds', ou 'map' mudarem.
  useEffect(() => {
    if (bounds && bounds.isValid && bounds.isValid()) {
      // Se 'bounds' (limites) for fornecido e for vlido (contm pontos vlidos)...
      // ...usamos 'map.fitBounds()' para ajustar a viso do mapa para mostrar todos os marcadores.
      // 'padding: [50, 50]' adiciona um preenchimento de 50px ao redor dos limites,
      // para que os marcadores no fiquem colados nas bordas do mapa.
      map.fitBounds(bounds, { padding: [50, 50] });
    } else if (
      center &&
      typeof center[0] === "number" &&
      typeof center[1] === "number"
    ) {
      // Seno, se 'center' for fornecido e for uma coordenada vlida...
      // ...usamos 'map.setView()' para centralizar o mapa no 'center' com o 'zoom' especificado.
      map.setView(center, zoom);
    }
    // Se nem 'bounds' nem 'center' vlidos forem fornecidos, a viso do mapa no  alterada por este componente.
  }, [center, zoom, bounds, map]); // Array de dependncias do useEffect.

  return null; // Este componente no renderiza nada visualmente no DOM. Ele apenas interage com o mapa.
};

// --- 4. DEFINIO DO COMPONENTE PRINCIPAL 'MapComponent' ---
// Recebe o objeto 'animal' como prop para acessar sua lista de pases nativos.
const MapComponent = ({ animal }) => {
  // --- A. VALORES PADRO PARA O MAPA ---
  // Usados se no pudermos calcular um centro/zoom especfico com base nos marcadores.
  const centroPadraoDoMapa = [-10, -55]; // Centro aproximado da Amrica do Sul.
  const zoomPadraoDoMapa = 3; // Nvel de zoom inicial.

  // --- B. ESTADO DO COMPONENTE ---
  // 'paisDestacado' armazena o nome base do pas que est atualmente destacado
  // (ex: quando o mouse passa sobre o nome do pas na lista abaixo do mapa).
  // Comea como 'null' (nenhum pas destacado).
  const [paisDestacado, definirPaisDestacado] = useState(null);

  // 'useRef' para armazenar referncias aos elementos dos marcadores do mapa.
  // 'markerRefs.current' ser um objeto onde as chaves so nomes de pases e os valores
  // so as instncias dos marcadores Leaflet.
  // No cdigo original, o uso disso para abrir popups estava comentado.
  // Vamos mant-lo para fins didticos, mostrando como se pode acessar elementos do mapa.
  const markerRefs = useRef({}); // Inicializa como um objeto vazio.

  // --- C. ACESSO  API KEY DO MAPTILER ---
  // IMPORTANTE: Esta chave  necessria para usar os mapas do MapTiler.
  // 'process.env.REACT_APP_MAPTILER_API_KEY' tenta ler a chave de uma varivel de ambiente.
  // Em projetos criados com Create React App, variveis de ambiente devem comear com REACT_APP_.
  // Para projetos com Vite, seria 'import.meta.env.VITE_MAPTILER_API_KEY'.
  //  crucial que esta chave NO seja colocada diretamente no cdigo e versionada no Git.
  // Ela deve estar em um arquivo .env (que  ignorado pelo Git) na raiz do projeto.
  // Exemplo de contedo do arquivo .env:
  // REACT_APP_MAPTILER_API_KEY=sua_chave_aqui
  const maptilerApiKey = process.env.REACT_APP_MAPTILER_API_KEY;

  // Este useEffect roda uma vez aps a montagem do componente para verificar a API Key.
  useEffect(() => {
    if (!maptilerApiKey) {
      console.warn(
        "MapComponent: Chave da API do MapTiler no configurada! O mapa pode no funcionar ou usar um provedor alternativo." +
          " Verifique suas variveis de ambiente (ex: REACT_APP_MAPTILER_API_KEY em um arquivo .env)." +
          " Para testes ou aprendizado, voc pode usar um TileLayer gratuito, como o do OpenStreetMap."
      );
    }
  }, [maptilerApiKey]); // Dependncia: maptilerApiKey (embora constante,  boa prtica listar se usada no efeito).

  // --- D. VERIFICAO INICIAL E PREPARAO DOS DADOS DO MAPA ---

  // Se, por algum motivo, os dados do 'animal' no forem fornecidos,
  // renderizamos uma mensagem de erro e no tentamos mostrar o mapa.
  if (!animal) {
    return (
      <p className="error-message">
        Dados do animal no disponveis para exibir o mapa de distribuio.
      </p>
    );
  }

  // Se a API Key no estiver disponvel, podemos optar por mostrar uma mensagem de erro de configurao
  // ou tentar usar um mapa alternativo (veja sugesto no console.warn acima).
  // Para esta verso didtica, vamos mostrar uma mensagem clara se a chave estiver faltando.
  if (!maptilerApiKey) {
    return (
      <section className={styles.mapSectionContainer}>
        <h2 className={styles.sectionTitle}>
          <span role="img" aria-label="cone de Mapa">
            
          </span>
          Distribuio Geogrfica e Pases Nativos
        </h2>
        <div
          className={styles.mapContainerWrapper}
          style={{
            textAlign: "center",
            padding: "20px",
            border: "1px dashed red",
          }}
        >
          <p style={{ color: "red", fontWeight: "bold", marginBottom: "10px" }}>
             Erro de Configurao do Mapa 
          </p>
          <p className={styles.textMuted}>
            A chave da API para o servio de mapas (MapTiler) no foi
            encontrada. Por favor, verifique as configuraes do projeto
            (arquivo <code>.env</code>) ou contate o administrador.
          </p>
          <p className={styles.textMuted} style={{ marginTop: "10px" }}>
            Como alternativa para desenvolvimento, voc pode usar o TileLayer
            padro do OpenStreetMap comentando a URL do MapTiler e descomentando
            a URL do OpenStreetMap no cdigo deste componente.
          </p>
        </div>
      </section>
    );
  }

  // Extrai o nome comum do animal para usar em mensagens ou ttulos.
  const nomeComumDoAnimal = get(
    animal,
    "nome_comum.pt",
    get(animal, "nome_cientifico")
  );
  // Pega a lista de pases nativos do animal. Espera-se que seja um array de strings.
  const listaDePaisesNativos = get(animal, "distribuicao.paises_nativos", []);

  // Funo auxiliar para extrair o nome base do pas (remove detalhes em parnteses).
  // Ex: "Estados Unidos (Arizona, Texas)" -> "Estados Unidos"
  const obterNomeBaseDoPais = (nomeCompletoDoPais) => {
    const indiceParenteses = nomeCompletoDoPais.indexOf("(");
    if (indiceParenteses !== -1) {
      return nomeCompletoDoPais.substring(0, indiceParenteses).trim();
    }
    return nomeCompletoDoPais.trim();
  };

  // Processa a lista de pases nativos para criar os marcadores do mapa:
  // Para cada nome de pas na lista, busca suas coordenadas no nosso objeto 'countryCoordinates'.
  const marcadoresDosPaisesNativos = listaDePaisesNativos
    .map((nomeDoPaisNoArray) => {
      // Obtm o nome base para consulta no objeto de coordenadas.
      const nomeBaseDoPais = obterNomeBaseDoPais(nomeDoPaisNoArray);
      // Busca as coordenadas [latitude, longitude] para este nome base.
      const coordenadas = countryCoordinates[nomeBaseDoPais];

      if (coordenadas && coordenadas.length === 2) {
        // Se as coordenadas foram encontradas e so vlidas (um array com 2 nmeros)...
        return {
          name: nomeDoPaisNoArray, // Nome completo do pas (pode ter detalhes em parnteses).
          position: coordenadas, // Array [latitude, longitude].
          baseName: nomeBaseDoPais, // Nome base, usado como chave para o estado 'paisDestacado'.
        };
      } else {
        // Se as coordenadas no foram encontradas, registra um aviso no console.
        console.warn(
          `MapComponent: Coordenadas no encontradas para o pas "${nomeBaseDoPais}" (nome original: "${nomeDoPaisNoArray}"). Este pas no ser marcado no mapa.`
        );
        return null; // Retorna null para este pas, ser filtrado depois.
      }
    })
    .filter((marker) => marker !== null); // Remove os itens nulos (pases sem coordenadas).

  // Calcula os limites (bounds) do mapa para ajustar a viso e mostrar todos os marcadores.
  // 'bounds' ser um objeto L.LatLngBounds do Leaflet.
  let boundsParaOMapa = null;
  if (marcadoresDosPaisesNativos.length > 0) {
    // Se houver marcadores, extrai todas as posies [lat, lng].
    const posicoesLatLng = marcadoresDosPaisesNativos.map(
      (marcador) => marcador.position
    );
    // Cria um objeto LatLngBounds que engloba todas essas posies.
    boundsParaOMapa = L.latLngBounds(posicoesLatLng);
  }

  // --- E. FUNES DE CALLBACK PARA INTERAO (Lista de Pases) ---

  // Chamada quando o mouse entra na rea de um item da lista de pases.
  const lidarComMouseEnterNoPaisDaLista = (nomeDoPais) => {
    const nomeBase = obterNomeBaseDoPais(nomeDoPais);
    definirPaisDestacado(nomeBase); // Define o estado para destacar o marcador correspondente no mapa.

    // Lgica opcional (originalmente comentada) para abrir o popup do marcador:
    // const markerInstance = markerRefs.current[nomeBase];
    // if (markerInstance) {
    //   markerInstance.openPopup();
    // }
  };

  // Chamada quando o mouse sai da rea de um item da lista de pases.
  const lidarComMouseLeaveDoPaisDaLista = () => {
    definirPaisDestacado(null); // Limpa o estado, nenhum pas destacado.
    // Poderia fechar popups aqui tambm, se a lgica acima fosse usada.
  };

  // --- F. RENDERIZAO DO COMPONENTE MapComponent ---
  return (
    <section className={styles.mapSectionContainer}>
      <h2 className={styles.sectionTitle}>
        <span role="img" aria-label="cone de Globo Terrestre">
          
        </span>{" "}
        {/* Melhor que mapa simples para distribuio */}
        Distribuio Geogrfica e Pases Nativos de {nomeComumDoAnimal}
      </h2>
      <p className={styles.mapIntroText}>
        O mapa abaixo destaca os pases onde esta espcie pode ser encontrada
        naturalmente. Passe o mouse sobre os nomes dos pases na lista para
        destacar o marcador no mapa.
      </p>
      <div className={styles.mapContainerWrapper}>
        {/* Container onde o mapa Leaflet ser renderizado. */}
        <div className={styles.mapDisplay}>
          <MapContainer
            // 'key={animal.id_animal || animal.codigo}': FORA A RECRIAO do MapContainer
            // se o animal mudar. Isso  importante porque o MapContainer
            // no reage bem a mudanas dinmicas de 'center' ou 'zoom' aps a montagem inicial
            // sem usar truques como o componente ChangeView. Recri-lo garante
            // que a viso inicial seja correta para o novo animal.
            key={get(animal, "id_animal", get(animal, "codigo", Math.random()))} // Fallback para chave aleatria se ID faltar.
            // Define o centro e zoom iniciais do mapa.
            // Se 'boundsParaOMapa' foi calculado, usa o centro desses limites.
            // Seno, usa os valores padro.
            center={
              boundsParaOMapa && boundsParaOMapa.isValid()
                ? boundsParaOMapa.getCenter()
                : centroPadraoDoMapa
            }
            zoom={zoomPadraoDoMapa} // O zoom inicial  padro; ChangeView pode ajust-lo.
            scrollWheelZoom={true} // Permite zoom com a roda do mouse.
            style={{ height: "100%", width: "100%" }} // Faz o mapa ocupar todo o seu container.
            // placeholder={<p>Carregando mapa...</p>} // Opcional: um placeholder enquanto o mapa carrega.
          >
            {/* Componente para mudar a viso do mapa dinamicamente */}
            <ChangeView
              bounds={boundsParaOMapa}
              center={centroPadraoDoMapa} // Passa o centro padro como fallback para ChangeView
              zoom={zoomPadraoDoMapa} // Passa o zoom padro
            />

            {/* Camada de "Azulejos" (Tile Layer) - O mapa base visual. */}
            {/* USA A CHAVE DO MAPTILER: */}
            <TileLayer
              url={`https://api.maptiler.com/maps/aquarelle/{z}/{x}/{y}.png?key=${maptilerApiKey}`} // Estilo "aquarelle"
              // Outras opes de estilo do MapTiler:
              // `https://api.maptiler.com/maps/streets-v2/{z}/{x}/{y}.png?key=${maptilerApiKey}`
              // `https://api.maptiler.com/maps/hybrid/{z}/{x}/{y}.jpeg?key=${maptilerApiKey}` (satlite com rtulos)
              attribution='&copy; <a href="https://www.maptiler.com/copyright/" target="_blank">MapTiler</a> &copy; <a href="https://www.openstreetmap.org/copyright" target="_blank">OpenStreetMap</a> contributors'
            />
            {/*
              ALTERNATIVA GRATUITA (SEM API KEY) - OpenStreetMap:
              Descomente a linha abaixo e comente a de cima se no tiver uma API Key do MapTiler.
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            />
            */}

            {/* Renderiza um Marcador (<Marker>) para cada pas nativo. */}
            {marcadoresDosPaisesNativos.map(({ name, position, baseName }) => (
              <Marker
                key={baseName} // Chave nica para cada marcador.
                position={position} // Coordenadas [latitude, longitude].
                // Define o cone do marcador: se o pas estiver destacado, usa 'highlightedIcon', seno 'defaultIcon'.
                icon={
                  paisDestacado === baseName ? highlightedIcon : defaultIcon
                }
                // Guarda uma referncia  instncia do marcador Leaflet (opcional, para interaes avanadas).
                ref={(el) => {
                  // 'el'  a instncia do marcador Leaflet.
                  // Armazenamos no objeto markerRefs.current, usando o nome base do pas como chave.
                  if (el) {
                    markerRefs.current[baseName] = el;
                  }
                }}
                // Eventos para o marcador (opcional):
                // eventHandlers={{
                //   mouseover: () => {
                //     definirPaisDestacado(baseName); // Destaca o pas na lista ao passar o mouse no marcador.
                //     // markerRefs.current[baseName]?.openPopup(); // Abre o popup
                //   },
                //   mouseout: () => {
                //     definirPaisDestacado(null);
                //     // markerRefs.current[baseName]?.closePopup(); // Fecha o popup
                //   },
                //   // click: () => { /* Alguma ao ao clicar no marcador */ }
                // }}
              >
                {/* Popup que aparece ao clicar no marcador. */}
                <Popup>{name}</Popup>{" "}
                {/* Mostra o nome completo do pas no popup. */}
              </Marker>
            ))}
          </MapContainer>
        </div>{" "}
        {/* Fim de .mapDisplay */}
        {/* Lista de Pases Nativos (renderizada abaixo do mapa). */}
        <div className={styles.countryListFallback}>
          <h3 className={styles.countryListTitle}>
            Pases onde o {nomeComumDoAnimal}  nativo:
          </h3>
          {/* Verifica se h pases na lista para exibir. */}
          {Array.isArray(listaDePaisesNativos) &&
          listaDePaisesNativos.length > 0 ? (
            <ul className={styles.countryList}>
              {listaDePaisesNativos.map((nomeDoPais) => {
                const nomeBase = obterNomeBaseDoPais(nomeDoPais);
                return (
                  <li
                    key={nomeDoPais} // Usa o nome completo como chave (deve ser nico na lista).
                    // Aplica a classe de destaque se este pas for o 'paisDestacado' no estado.
                    className={`${styles.countryListItem} ${
                      paisDestacado === nomeBase
                        ? styles.countryListItemHighlighted
                        : ""
                    }`}
                    // Eventos para interatividade com o mapa.
                    onMouseEnter={() =>
                      lidarComMouseEnterNoPaisDaLista(nomeDoPais)
                    }
                    onMouseLeave={lidarComMouseLeaveDoPaisDaLista}
                    // Opcional: onClick para centralizar o mapa no pas ou algo similar.
                    // onClick={() => {
                    //   const markerInfo = marcadoresDosPaisesNativos.find(m => m.baseName === nomeBase);
                    //   if (markerInfo && markerRefs.current[nomeBase]) {
                    //     const map = markerRefs.current[nomeBase]._map; // Acesso  instncia do mapa (requer cuidado)
                    //     if(map) map.setView(markerInfo.position, 5); // Exemplo: centraliza e define zoom
                    //   }
                    // }}
                  >
                    {nomeDoPais}
                  </li>
                );
              })}
            </ul>
          ) : (
            // Mensagem se a lista de pases nativos estiver vazia ou no disponvel.
            <p className={styles.textMuted}>
              Informaes sobre os pases nativos no disponveis para esta
              espcie.
            </p>
          )}
        </div>{" "}
        {/* Fim de .countryListFallback */}
      </div>{" "}
      {/* Fim de .mapContainerWrapper */}
    </section> // Fim de .mapSectionContainer
  );
};

export default MapComponent;
