// Caminho: src/components/AnimalList.jsx

// =================================================================================================
// ARQUIVO: AnimalList.jsx
// PROPSITO: Este componente  o responsvel por exibir uma coleo (lista) de cards de animais.
//            Ele recebe um array de objetos de animais do seu componente pai (App.js)
//            e, para cada animal nesse array, ele renderiza (mostra na tela) um
//            componente <AnimalCard />.
//
// CONCEITOS PRINCIPAIS DO REACT E JAVASCRIPT UTILIZADOS AQUI:
//  - Componentes Funcionais: A forma padro de criar componentes em React.
//  - Props (Propriedades): Como o componente recebe dados e funes de seu pai.
//      - 'animals': Um array de objetos. Cada objeto contm as informaes de um animal.
//      - 'onSelectAnimal': Uma funo (callback) que  passada para cada AnimalCard.
//                         Quando um AnimalCard  clicado, essa funo  chamada,
//                         permitindo que o App.js (o pai) saiba qual animal foi selecionado.
//  - Renderizao de Listas com '.map()': O mtodo de array '.map()' do JavaScript  usado
//                                       para transformar cada item de um array de dados
//                                       em um elemento React (um componente <AnimalCard />).
//  - A Prop Especial 'key': Uma prop obrigatria e nica que o React exige ao renderizar
//                           listas de elementos. Ela ajuda o React a identificar
//                           eficientemente quais itens mudaram, foram adicionados ou removidos.
//  - Renderizao Condicional: Verificar se a lista de animais est vazia antes de
//                              tentar renderizar os cards.
//  - Composio de Componentes: O AnimalList "compe" sua interface usando mltiplos
//                               componentes AnimalCard.
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS E COMPONENTES ---

// Importamos a biblioteca 'React'.  a base para tudo em React,
// permitindo-nos definir componentes e usar JSX.
import React from "react";

// Importamos o componente 'AnimalCard'.
// O AnimalList vai criar uma instncia de <AnimalCard /> para cada animal
// que ele precisar exibir. Isso  um exemplo de "composio" de componentes.
import AnimalCard from "./AnimalCard";

// Importamos o objeto 'styles' do nosso arquivo CSS Module (AnimalList.module.css).
// As classes definidas em 'AnimalList.module.css' (ex: .animalListContainer)
// sero usadas para aplicar estilos ao layout desta lista de cards.
// Ex: <section className={styles.animalListContainer}>
import styles from "./AnimalList.module.css";

// --- 2. DEFINIO DO COMPONENTE FUNCIONAL 'AnimalList' ---

// O componente AnimalList  uma funo que aceita um objeto de 'props' como argumento.
// Usamos a desestruturao de props ({ animals, onSelectAnimal }) para extrair
// diretamente as propriedades que esperamos receber do componente pai (App.js).
const AnimalList = ({ animals, onSelectAnimal }) => {
  // --- A. VERIFICAO INICIAL: H ANIMAIS PARA EXIBIR? ---
  // Antes de tentarmos criar uma lista de cards,  uma boa prtica verificar se
  // realmente temos animais para mostrar.

  // Condio:
  // - '!animals': Verifica se a prop 'animals'  'undefined', 'null', 'false', 0, NaN ou uma string vazia.
  //               Se 'animals' no for passado, esta condio ser verdadeira.
  // - 'animals.length === 0': Verifica se o array 'animals' est vazio.
  if (!animals || animals.length === 0) {
    // Se no houver animais, retornamos um pargrafo <p> com uma mensagem para o usurio.
    // Este 'return' interrompe a execuo da funo AnimalList aqui, e o JSX
    // da lista de cards (mais abaixo) no ser processado nem renderizado.

    // Aplicamos alguns estilos inline simples para a mensagem.
    // Em um projeto maior, essa mensagem poderia ser um componente separado ou usar classes globais.
    // Estamos usando variveis CSS globais (ex: --color-text-muted) que foram definidas em src/index.css.
    return (
      <p
        style={{
          textAlign: "center", // Centraliza o texto.
          color: "var(--color-text-muted)", // Cor cinza para texto secundrio.
          fontSize: "1.25rem", // Tamanho da fonte um pouco maior.
          padding: "2.5rem 1rem", // Espaamento interno (2.5rem em cima/baixo, 1rem nas laterais).
          marginTop: "1rem", // Um pouco de espao acima.
        }}
      >
        Ops! Nenhum animalzinho encontrado com os filtros atuais. Que tal tentar
        uma nova busca? 
      </p>
    );
  }

  // --- B. RENDERIZAO DA LISTA DE CARDS DE ANIMAIS ---
  // Se a verificao acima passou, significa que temos um array 'animals' com pelo menos um animal.
  // Agora, vamos construir a lista de <AnimalCard />.

  // Usamos a tag <section> do HTML5, que  semanticamente apropriada para agrupar
  // um conjunto de contedo relacionado (neste caso, a lista de cards de animais).
  // - id="animal-list": Um ID nico para esta seo. Pode ser til para links ncora internos na pgina
  //                     ou para seleo em testes automatizados, mas no  estritamente necessrio para o React.
  // - className={styles.animalListContainer}: Aplica a classe CSS '.animalListContainer'
  //                                          definida em 'AnimalList.module.css'. Esta classe
  //                                          ser responsvel pelo layout em grade dos cards.
  return (
    <section id="animal-list" className={styles.animalListContainer}>
      {/*
        A Mgica do '.map()': Transformando Dados em Componentes React
        - 'animals.map(...)': O mtodo '.map()'  uma das funes mais importantes para trabalhar
          com arrays em JavaScript e, por consequncia, em React para renderizar listas.
          Ele NO modifica o array original 'animals'. Em vez disso, ele CRIA UM NOVO ARRAY.
        - Como funciona:
          1. Ele itera (passa por) cada item do array 'animals'.
          2. Para cada 'animalObject' encontrado no array, ele executa a funo que passamos
             (a "arrow function" `(animalObject) => { ... }`).
          3. O valor RETORNADO por essa funo para cada animal se torna um item no NOVO array.
          4. No nosso caso, a funo retorna um componente <AnimalCard />.
             Ento, o resultado de 'animals.map(...)' ser um array de componentes <AnimalCard />.
             Ex: [<AnimalCard ... />, <AnimalCard ... />, <AnimalCard ... />]
        - O React ento pega este array de componentes e os renderiza na tela.
      */}
      {animals.map((animalObject) => {
        // Para cada 'animalObject' na lista 'animals':

        // Verificao opcional (para robustez extra, embora App.js deva garantir IDs):
        // Se o animal no tiver um ID, podemos registrar um aviso ou pular este animal.
        const animalId = animalObject.id_animal || animalObject.codigo;
        if (!animalId) {
          console.warn(
            "AnimalList: Encontrado um animal sem 'id_animal' ou 'codigo'. Pulando renderizao deste card:",
            animalObject
          );
          return null; // Retornar null faz com que o React no renderize nada para este item.
        }

        // Retornamos um componente <AnimalCard /> para este animal especfico.
        return (
          <AnimalCard
            // A Prop 'key': EXTREMAMENTE IMPORTANTE para listas em React!
            // - Propsito: O React usa a 'key' para identificar unicamente cada elemento
            //              gerado dentro de uma lista (como os <AnimalCard /> aqui).
            //              Isso permite que o React seja muito eficiente ao atualizar a lista.
            //              Por exemplo, se um animal for adicionado, removido ou a lista for
            //              reordenada, o React usa as 'keys' para saber exatamente quais
            //              elementos mudaram, em vez de ter que recriar todos eles.
            // - Valor da 'key': Deve ser uma STRING ou NMERO que seja NICO entre
            //                   os "irmos" na lista (ou seja, nico entre todos os AnimalCards
            //                   gerados por este .map()).
            // - MELHOR PRTICA: Use um ID estvel e nico que venha dos seus dados.
            //                  No nosso caso, 'animalObject.id_animal' ou 'animalObject.codigo' so boas escolhas.
            // - EVITE USAR O NDICE DO ARRAY (ex: `key={index}`) como chave se a ordem dos
            //   itens na lista puder mudar, ou se itens puderem ser inseridos/removidos
            //   no meio da lista. Usar o ndice nesses casos pode levar a bugs
            //   comportamento inesperado e problemas de performance.
            key={animalId} // Usamos o ID nico do animal como chave.
            // Passando Props para o Componente AnimalCard:
            // Aqui, estamos passando duas props para cada <AnimalCard />:

            // 1. prop 'animal':
            //    Passamos o 'animalObject' completo (o objeto com todas as informaes
            //    do animal atual da iterao do .map()) para o AnimalCard.
            //    Dentro do componente AnimalCard, ele poder acessar este objeto
            //    atravs de 'props.animal'.
            animal={animalObject}
            // 2. prop 'onSelectAnimal':
            //    Repassamos a funo 'onSelectAnimal' que o AnimalList recebeu do seu pai (App.js).
            //    Isso permite que cada AnimalCard individual, ao ser clicado, chame esta funo.
            //    A funo 'onSelectAnimal' no App.js saber ento qual animal foi clicado
            //    (porque o AnimalCard passar o ID do animal para ela).
            //    Este  um padro comum em React para comunicao de filho para pai.
            onSelectAnimal={onSelectAnimal}
          />
        );
      })}{" "}
      {/* Fim da funo .map() e do array de AnimalCards */}
    </section> // Fim da tag <section> que contm a lista
  );
}; // Fim da definio do componente AnimalList

// --- 3. EXPORTAO DO COMPONENTE ---
// 'export default AnimalList;' torna o componente AnimalList disponvel para
// ser importado e utilizado em outros arquivos da aplicao, como no App.js.
export default AnimalList;
