// Caminho: src/components/AnimalDetail.jsx

// =================================================================================================
// ARQUIVO: AnimalDetail.jsx
// PROPSITO: Mostrar uma pgina com todas as informaes detalhadas sobre UM NICO animal
//            que foi selecionado pelo usurio na lista principal.
//
// CONCEITOS PRINCIPAIS DO REACT UTILIZADOS AQUI:
//  - Componentes Funcionais: A estrutura bsica do nosso componente.
//  - Props: Como o componente recebe dados (o objeto 'animal') e funes (a funo 'onBack')
//           do seu componente pai (o App.js).
//  - useState: Para gerenciar o estado interno do componente, como:
//              - Se o "tazo" do animal no cabealho est virado ou no.
//              - Qual imagem est sendo exibida no "lightbox" (visualizador de imagem em tela cheia).
//              - Se o lightbox est visvel ou no.
//  - useEffect: Para adicionar e remover "escutadores de eventos" (event listeners),
//               especificamente para permitir que o usurio feche o lightbox pressionando a tecla "Esc".
//  - useMemo: Para otimizar o processamento de dados que no mudam frequentemente, como a lista
//             de imagens da galeria. Ele "memoriza" o resultado de uma computao e s a refaz
//             se as suas dependncias mudarem.
//  - JSX: A sintaxe especial que usamos para descrever a estrutura da interface do usurio.
//  - Renderizao Condicional: Mostrar ou esconder partes da interface com base em certas condies
//                           (ex: mostrar o lightbox apenas se uma imagem estiver selecionada para ampliao).
//  - Mapeamento de Arrays para Elementos (listas): Usar o mtodo '.map()' para transformar arrays de dados
//                                                (ex: lista de curiosidades, lista de imagens da galeria)
//                                                em uma lista de elementos JSX.
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS, COMPONENTES E FUNES ---

// Importa a biblioteca React e os Hooks que vamos utilizar.
import React, { useState, useEffect, useMemo } from "react";

// Importa o componente MapComponent, que ser usado para mostrar o mapa de distribuio do animal.
import MapComponent from "./MapComponent";

// Importa funes utilitrias do nosso arquivo de dados (animalData.js).
// - 'get': Para buscar dados dentro de objetos de forma segura e com um valor padro.
// - 'getExtinctionHexColor': Para obter o cdigo hexadecimal da cor correspondente ao status de extino.
import { get, getExtinctionHexColor } from "../data/animalData";

// Importa o objeto 'styles' do nosso arquivo CSS Module (AnimalDetail.module.css).
// Isso nos permite usar classes CSS como 'styles.detailContainer', 'styles.sectionTitle', etc.,
// e garante que esses estilos sejam escopados (limitados) apenas a este componente.
import styles from "./AnimalDetail.module.css";


// --- 2. COMPONENTE AUXILIAR INTERNO: DetailSectionContent ---
// Este  um "componente auxiliar" (helper component) criado para ajudar a padronizar
// a forma como cada seo de informao (Habitat, Ecologia, etc.)  exibida.
// Ele no ser exportado, pois s  usado dentro do AnimalDetail.jsx.
//
// Props que este componente espera receber:
//  - title: (String) O ttulo da seo (ex: "Habitat").
//  - children: (JSX) O contedo principal da seo. Esta  uma prop especial do React.
//                   Tudo que  colocado entre as tags <DetailSectionContent> e </DetailSectionContent>
//                   ser passado como 'children'.
//  - icon: (String, opcional) Um emoji ou caractere para ser exibido antes do ttulo.
//  - condition: (Boolean, opcional, padro  true) Se false, a seo no renderiza seu contedo,
//               mostrando apenas a mensagem de "Informao no disponvel".
const DetailSectionContent = ({ title, children, icon = "", condition = true }) => {
  // Vamos verificar se o contedo ('children') que recebemos  considerado "vazio" ou "invlido".
  let conteudoConsideradoVazio = false;
  if (
    children === null || // Se for explicitamente null
    children === undefined || // Se for explicitamente undefined
    children === "N/A" || // Se a funo 'get' retornou "N/A" (nosso valor padro para dados ausentes)
    (Array.isArray(children) && children.length === 0) || // Se for um array sem nenhum item
    (typeof children === 'object' && // Se for um objeto...
      !React.isValidElement(children) && // ... que NO seja um elemento React vlido (como <p> ou outro componente)...
      Object.keys(children).length === 0) // ... e que NO tenha nenhuma propriedade prpria.
  ) {
    conteudoConsideradoVazio = true;
  }

  // Se a 'condition' prop for 'false', OU se o 'conteudoConsideradoVazio' for 'true',
  // ento vamos renderizar o ttulo da seo seguido de uma mensagem padro.
  if (!condition || conteudoConsideradoVazio) {
    return (
      <div className={styles.detailSection}> {/* Container da seo */}
        <h2 className={styles.sectionTitle}> {/* Ttulo da seo */}
          {icon && ( /* Renderiza o cone SOMENTE SE a prop 'icon' foi fornecida E no  vazia */
            <span className={styles.icon} role="img" aria-label={title.toLowerCase()}>
              {icon}
            </span>
          )}
          {title} {/* O ttulo da seo */}
        </h2>
        {/* Mensagem padro para quando no h contedo */}
        <p className={styles.textMuted}>Informao no disponvel para esta seo.</p>
      </div>
    );
  }

  // Se a 'condition' for 'true' E o contedo NO for considerado vazio,
  // renderizamos a seo normalmente com seu ttulo e o contedo ('children').
  return (
    <div className={styles.detailSection}>
      <h2 className={styles.sectionTitle}>
        {icon && (
          <span className={styles.icon} role="img" aria-label={title.toLowerCase()}>
            {icon}
          </span>
        )}
        {title}
      </h2>
      {children} {/* Aqui o 'children' (contedo JSX passado)  renderizado. */}
    </div>
  );
};


// --- 3. MAPA PARA EXIBIO AMIGVEL DE NOMES DE IDIOMAS ---
// Este objeto  usado para traduzir os cdigos de idioma (ex: "pt", "en")
// para nomes mais legveis quando formos listar os "Nomes Comuns em outros idiomas".
const languageDisplayMap = {
  pt: "Portugus (Brasil)", // Poderamos ser mais especficos
  en: "Ingls (English)",
  es: "Espanhol (Espaol)",
  fr: "Francs (Franais)", // Exemplo, adicione mais conforme necessrio
  de: "Alemo (Deutsch)",   // Exemplo
  // Adicione outros cdigos de idioma e seus nomes de exibio
  // baseados nos dados que voc tem em `animal.nome_comum`.
};


// --- 4. DEFINIO DO COMPONENTE PRINCIPAL 'AnimalDetail' ---
// Este componente recebe duas props principais do App.js:
//  - 'animal': Um objeto contendo todos os dados do animal que foi selecionado.
//  - 'onBack': Uma funo que ser chamada quando o usurio clicar no boto "Voltar  Lista".
//              Esta funo  definida no App.js e geralmente atualiza o estado l
//              para indicar que nenhum animal est mais selecionado, fazendo a lista reaparecer.
const AnimalDetail = ({ animal, onBack }) => {
  // --- A. GERENCIAMENTO DE ESTADO INTERNO DO COMPONENTE ---
  // Usamos 'useState' para criar variveis de estado que este componente precisa "lembrar".

  // Estado para controlar o lightbox (visualizador de imagem ampliada):
  // - 'lightboxInfo.index': O ndice da imagem que est atualmente ampliada.
  //                         Comea com -1, o que significa que o lightbox est fechado.
  // - 'lightboxInfo.images': Um array contendo as URLs de todas as imagens que podem
  //                          ser vistas no lightbox (geralmente as imagens da galeria).
  const [lightboxInfo, definirLightboxInfo] = useState({ index: -1, images: [] });

  // Estado para controlar se o "tazo" (card com imagem frente/verso)
  // exibido no cabealho desta pgina de detalhes est virado ou no.
  // Comea como 'false' (mostrando a frente).
  const [tazoCabecalhoVirado, definirTazoCabecalhoVirado] = useState(false);


  // --- B. MEMORIZAO DO CLCULO DAS IMAGENS DA GALERIA (useMemo) ---
  // 'useMemo'  um Hook de otimizao. Ele "memoriza" (guarda o resultado de)
  // uma funo de clculo. A funo s  re-executada se uma das "dependncias"
  // (listadas no array no final do useMemo) mudar.
  //
  // Neste caso, estamos calculando a lista de URLs para as imagens da galeria.
  // Este clculo s precisa ser feito novamente se o objeto 'animal' (a prop) mudar.
  // Se o componente AnimalDetail re-renderizar por outros motivos (ex: mudana de estado interno),
  // 'imagensDaGaleria' no ser recalculado desnecessariamente, usando o valor memorizado.
  const imagensDaGaleria = useMemo(() => {
    // console.log("Calculando imagensDaGaleria..."); // Para depurao, mostra quando  recalculado.

    // Se no recebermos dados do 'animal' ou se o animal no tiver a propriedade 'imagens',
    // retornamos um array vazio para evitar erros.
    if (!animal || !animal.imagens) {
      return [];
    }

    // Usamos um 'Set' para garantir que cada URL de imagem seja nica na galeria,
    // evitando duplicatas caso os dados as contenham.
    const urlsUnicas = new Set();
    const imagensOrdenadasParaGaleria = [];

    // Funo interna (helper) para adicionar uma URL de imagem  lista,
    // apenas se ela for uma string vlida, no vazia, e ainda no estiver na lista.
    const adicionarImagemSeValida = (url) => {
      if (url && typeof url === 'string' && url.trim() !== "" && !urlsUnicas.has(url)) {
        urlsUnicas.add(url); // Adiciona ao Set para marcar como j vista.
        imagensOrdenadasParaGaleria.push(url); // Adiciona ao array final.
      }
    };

    // Tentamos adicionar as imagens 'foto_1' e 'foto_2'  galeria.
    // Usamos a funo 'get' para buscar essas URLs de forma segura no objeto 'animal.imagens'.
    adicionarImagemSeValida(get(animal, "imagens.foto_1", "")); // Se 'foto_1' no existir, 'get' retorna "".
    adicionarImagemSeValida(get(animal, "imagens.foto_2", ""));

    // Poderamos adicionar mais imagens aqui se a estrutura de dados tivesse, por exemplo:
    // adicionarImagemSeValida(get(animal, "imagens.foto_adicional_1", ""));
    // adicionarImagemSeValida(get(animal, "imagens.mapa_distribuicao_img", ""));

    return imagensOrdenadasParaGaleria; // Retorna o array com as URLs das imagens da galeria.
  }, [animal]); // Array de dependncias: 'useMemo' re-executar esta funo SE 'animal' mudar.


  // --- C. FUNES PARA CONTROLAR O LIGHTBOX (VISUALIZADOR DE IMAGENS) ---

  // Funo para ABRIR o lightbox.
  //  chamada quando o usurio clica em uma imagem da galeria ou no tazo do cabealho.
  // 'urlDaImagemClicada': A URL da imagem que o usurio clicou.
  const abrirImagemNoLightbox = (urlDaImagemClicada) => {
    // Primeiro, tentamos encontrar o ndice da imagem clicada dentro da nossa lista 'imagensDaGaleria'.
    const indiceDaImagemNaGaleria = imagensDaGaleria.findIndex(url => url === urlDaImagemClicada);

    if (indiceDaImagemNaGaleria !== -1) {
      // Se a imagem clicada FAZ PARTE da galeria, configuramos o lightbox para comear
      // nessa imagem e disponibilizamos todas as imagens da galeria para navegao.
      definirLightboxInfo({
        index: indiceDaImagemNaGaleria, // O ndice da imagem clicada.
        images: imagensDaGaleria        // O array completo de imagens da galeria.
      });
    } else if (urlDaImagemClicada && typeof urlDaImagemClicada === 'string' && urlDaImagemClicada.trim() !== "") {
      // Se a imagem clicada NO est na galeria (ex: pode ser uma das imagens do tazo do cabealho
      // que no repetimos na galeria), abrimos o lightbox mostrando APENAS essa imagem.
      // Nesse caso, a navegao (prxima/anterior) no far sentido ou no estar disponvel.
      definirLightboxInfo({
        index: 0, // Comeamos no ndice 0 do novo array de imagens.
        images: [urlDaImagemClicada] // O array de imagens contm apenas a imagem clicada.
      });
    }
    // Se 'urlDaImagemClicada' for invlida, no fazemos nada.
  };

  // Funo para FECHAR o lightbox.
  //  chamada quando o usurio clica no backdrop (fundo escuro) do lightbox,
  // no boto de fechar (X), ou pressiona a tecla 'Escape'.
  const fecharLightbox = () => {
    // Reseta o estado do lightbox para seus valores iniciais (fechado).
    definirLightboxInfo({ index: -1, images: [] });
  };

  // Funo para navegar para a PRXIMA imagem no lightbox.
  // O argumento 'e'  o objeto do evento de clique (precisamos dele para parar a propagao).
  const proximaImagemLightbox = (e) => {
    // 'e.stopPropagation()' impede que o evento de clique neste boto
    // "borbulhe" para cima e acione o onClick do backdrop do lightbox (que fecharia o lightbox).
    e.stopPropagation();

    // Atualizamos o estado 'lightboxInfo' usando a forma funcional de 'definirLightboxInfo'.
    // Isso nos d acesso ao 'estadoAnterior' (o valor atual de lightboxInfo).
    definirLightboxInfo(estadoAnterior => ({
      ...estadoAnterior, // Mantemos todas as outras propriedades do estado (como o array 'images').
      // Calculamos o novo 'index':
      // (estadoAnterior.index + 1) pega o prximo ndice.
      // '% estadoAnterior.images.length' faz com que, se chegarmos ao final da lista de imagens,
      // o ndice volte para 0 (criando um efeito de carrossel/loop).
      index: (estadoAnterior.index + 1) % estadoAnterior.images.length,
    }));
  };

  // Funo para navegar para a imagem ANTERIOR no lightbox.
  const imagemAnteriorLightbox = (e) => {
    e.stopPropagation(); // Impede que o clique feche o lightbox.
    definirLightboxInfo(estadoAnterior => ({
      ...estadoAnterior,
      // Calcula o ndice anterior:
      // (estadoAnterior.index - 1 + estadoAnterior.images.length) garante que, se estivermos no ndice 0
      // e subtrairmos 1 (resultando em -1), ao somar o tamanho do array, obtemos um nmero positivo.
      // O operador '%' (mdulo) ento nos d o ndice correto para o final da lista (efeito carrossel).
      index: (estadoAnterior.index - 1 + estadoAnterior.images.length) % estadoAnterior.images.length,
    }));
  };


  // --- D. EFEITO COLATERAL (useEffect) PARA OUVIR A TECLA 'ESCAPE' ---
  // Este 'useEffect'  usado para adicionar um "escutador de eventos" (event listener)
  // ao documento, para que possamos fechar o lightbox quando o usurio pressionar a tecla 'Escape'.
  useEffect(() => {
    // Esta  a funo que ser chamada toda vez que uma tecla for pressionada NO DOCUMENTO INTEIRO.
    const lidarComTeclaEsc = (evento) => {
      // Verificamos se a tecla pressionada foi a 'Escape'.
      if (evento.key === "Escape") {
        fecharLightbox(); // Se sim, chamamos nossa funo para fechar o lightbox.
      }
    };

    // S adicionamos o escutador de eventos SE o lightbox estiver atualmente aberto.
    // Verificamos isso checando se 'lightboxInfo.index'  diferente de -1.
    if (lightboxInfo.index !== -1) {
      // 'document.addEventListener' anexa nossa funo 'lidarComTeclaEsc' ao evento 'keydown'
      // (que  disparado quando uma tecla  pressionada).
      document.addEventListener("keydown", lidarComTeclaEsc);
      // console.log("useEffect: Event listener para 'Esc' ADICIONADO."); // Para depurao
    }

    // A funo retornada pelo 'useEffect'  chamada de "funo de limpeza" (cleanup function).
    // Ela  executada:
    //  1. ANTES que o efeito seja executado novamente (se alguma de suas dependncias mudar).
    //  2. QUANDO o componente 'AnimalDetail' for "desmontado" (removido da tela).
    //  MUITO IMPORTANTE remover event listeners que foram adicionados manualmente
    // para evitar "vazamentos de memria" (memory leaks) e comportamentos inesperados.
    return () => {
      document.removeEventListener("keydown", lidarComTeclaEsc);
      // console.log("useEffect: Event listener para 'Esc' REMOVIDO."); // Para depurao
    };

  // Array de dependncias do useEffect: [lightboxInfo.index]
  // Este efeito ser re-executado (ou seja, o listener ser potencialmente re-adicionado/removido)
  // SOMENTE se o valor de 'lightboxInfo.index' mudar.
  // Isso garante que o listener s esteja ativo quando o lightbox estiver visvel.
  }, [lightboxInfo.index]);


  // --- E. VERIFICAO DE DADOS DO ANIMAL ---
  // Se, por algum motivo muito estranho, o componente AnimalDetail for renderizado
  // mas a prop 'animal' for nula ou indefinida, exibimos uma mensagem de erro
  // e interrompemos a renderizao do restante para evitar erros piores.
  if (!animal) {
    // Idealmente, o componente App.js j garantiria que 'animal' sempre tenha dados
    // antes de renderizar AnimalDetail, mas  uma boa prtica defensiva.
    return <p className="error-message">Erro crtico: Dados do animal no foram fornecidos para a pgina de detalhes.</p>;
  }


  // --- F. EXTRAO DE DADOS DO ANIMAL PARA USO NO JSX ---
  // Aqui, pegamos todas as informaes do objeto 'animal' que queremos exibir.
  // Usar 'get' torna o cdigo mais robusto a dados ausentes.

  // Informaes sobre o Nvel de Extino
  const objetoNivelExtincao = get(animal, "nivel_extincao", {}); // Pega o objeto inteiro para evitar mltiplas chamadas 'get' no mesmo nvel.
  const categoriaExtincao = get(objetoNivelExtincao, "categoria", "N/A");
  const descricaoExtincao = get(objetoNivelExtincao, "descricao", "No Avaliado");
  const corDoStatusHex = getExtinctionHexColor(categoriaExtincao); // Cor para o banner e escala.
  // Garante que 'escalaExtincao' seja sempre um array. Se 'get' retornar "N/A" ou algo que no seja array, usa [].
  const escalaExtincao = Array.isArray(get(objetoNivelExtincao, "escala", [])) ? get(objetoNivelExtincao, "escala", []) : [];

  // URLs das imagens do Tazo para o cabealho da pgina.
  const tazoFrontUrl = get(animal, "imagens.front", ""); // Retorna "" se no houver, para facilitar verificaes.
  const tazoBackUrl = get(animal, "imagens.back", "");

  // Nomes principais do animal.
  const nomeComumPrincipal = get(animal, "nome_tazo", "Nome Indisponvel");
  const nomeCientificoPrincipal = get(animal, "nome_cientifico", "Nome Cientfico Indisponvel");

  // Prepara a lista de Nomes Comuns em outros idiomas.
  const nomesComunsEmOutrosIdiomasRaw = get(animal, "nome_comum", {}); // Pega o objeto {pt: "...", en: "..."}
  const nomesComunsParaExibicao = {}; // Objeto para armazenar os nomes formatados.
  // Verifica se 'nomesComunsEmOutrosIdiomasRaw'  um objeto e no nulo.
  if (typeof nomesComunsEmOutrosIdiomasRaw === 'object' && nomesComunsEmOutrosIdiomasRaw !== null) {
    // Itera sobre as chaves (cdigos de idioma) do objeto.
    for (const codigoIdioma in nomesComunsEmOutrosIdiomasRaw) {
      // 'hasOwnProperty' garante que estamos olhando apenas para propriedades diretas do objeto,
      // e no para propriedades herdadas do prottipo (boa prtica).
      if (nomesComunsEmOutrosIdiomasRaw.hasOwnProperty(codigoIdioma)) {
        const nomeNesteIdioma = nomesComunsEmOutrosIdiomasRaw[codigoIdioma];
        // S adiciona se o nome for vlido e no a string "N/A".
        if (nomeNesteIdioma && nomeNesteIdioma !== "N/A") {
          // Usa o 'languageDisplayMap' para obter um nome de idioma amigvel (ex: "Portugus").
          // Se o cdigo do idioma no estiver no mapa, usa o prprio cdigo em maisculas como fallback.
          const nomeAmigavelDoIdioma = languageDisplayMap[codigoIdioma] || codigoIdioma.toUpperCase();
          // Se o nome para um idioma for um array de nomes (ex: para Espanhol),
          // junta os nomes com uma vrgula e espao. Seno, usa o nome como est.
          nomesComunsParaExibicao[nomeAmigavelDoIdioma] = Array.isArray(nomeNesteIdioma)
            ? nomeNesteIdioma.join(", ")
            : nomeNesteIdioma;
        }
      }
    }
  }

  // Funo auxiliar interna para renderizar listas de itens (array de strings).
  // Usada para sees como "Ameaas", "Curiosidades".
  // 'items': o array de strings.
  // 'iconeDoItem': um emoji ou string para prefixar cada item.
  const renderizarListaDeItensSimples = (items, iconeDoItem = "") => {
    // Se 'items' no for um array, ou estiver vazio, ou o nico item for "N/A" ou uma string vazia,
    // consideramos que no h dados vlidos para listar. Retornamos 'null' para que o
    // componente DetailSectionContent possa mostrar a mensagem de "Informao no disponvel".
    if (!Array.isArray(items) || items.length === 0 || (items.length === 1 && (!items[0] || items[0] === "N/A"))) {
      return null;
    }
    // Se houver itens vlidos, usamos '.map()' para transformar cada string do array em um elemento <li>.
    return (
      <ul className={styles.list}> {/* Aplica a classe CSS para estilizao da lista. */}
        {items.map((item, index) => (
          // 'key={index}'  importante para o React identificar cada item da lista de forma eficiente.
          // Idealmente, se os itens tivessem IDs nicos, usaramos esses IDs na key.
          // Usar o ndice como key s  seguro se a lista no for reordenada ou itens no forem inseridos/removidos do meio.
          <li key={index} className={styles.listItem}>
            {iconeDoItem && ( // Se um cone foi fornecido...
              <span className={styles.icon} role="img" aria-hidden="true">{iconeDoItem}</span> // ...renderiza o cone.
            )}
            {item} {/* O texto do item da lista. */}
          </li>
        ))}
      </ul>
    );
  };

  // Funo auxiliar interna para renderizar "listas de definio" (objetos chave-valor).
  // Usada para sees como "Classificao Taxonmica", "Ecologia".
  // 'dataObject': o objeto cujas propriedades queremos listar.
  const renderizarListaDeDefinicaoDetalhada = (dataObject) => {
    // Se 'dataObject' no for um objeto vlido ou estiver vazio...
    if (!dataObject || typeof dataObject !== 'object' || Object.keys(dataObject).length === 0) {
      return null; // ...retornamos null, e DetailSectionContent mostrar a mensagem padro.
    }
    // 'Object.entries(dataObject)' transforma o objeto em um array de pares [chave, valor].
    // Ex: { a: 1, b: 2 } se torna [ ['a', 1], ['b', 2] ].
    // Filtramos para manter apenas as entradas onde o valor  "significativo" (no nulo, no "N/A").
    const entradasValidasDoObjeto = Object.entries(dataObject).filter(
      ([chaveDaPropriedade, valorDaPropriedade]) => valorDaPropriedade && valorDaPropriedade !== "N/A"
    );

    if (entradasValidasDoObjeto.length === 0) {
      return null; // Se no houver entradas vlidas aps o filtro.
    }

    // Usamos <dl> (definition list) para semntica, embora a estilizao seja controlada por classes.
    return (
      <dl className={styles.list}> {/* Reutiliza a classe .list para espaamento entre os itens. */}
        {entradasValidasDoObjeto.map(([chave, valor]) => (
          // Cada par chave-valor  envolvido em uma <div> com a classe .listItem para estilo.
          <div key={chave} className={styles.listItem}>
            {/* <dt> (definition term) para a chave/nome da propriedade. */}
            <dt className={styles.definitionListDt}>
              {/* Formatamos a chave: trocamos '_' por espao e capitalizamos a primeira letra de cada palavra. */}
              {chave.replace(/_/g, " ").replace(/\b\w/g, letra => letra.toUpperCase())}:
            </dt>
            {/* <dd> (definition description) para o valor da propriedade. */}
            <dd className={styles.definitionListDd}>{String(valor)}</dd> {/* Convertemos o valor para String para garantir. */}
          </div>
        ))}
      </dl>
    );
  };


  // --- G. RENDERIZAO FINAL DO COMPONENTE AnimalDetail ---
  return (
    // Fragmento React (<>...</>) para agrupar os elementos sem adicionar um n <div> extra ao DOM.
    <>
      {/* O container principal da pgina de detalhes do animal. */}
      <article className={styles.detailContainer}>
        {/* Boto para Voltar  Lista:
            - onClick={onBack}: Chama a funo 'onBack' (recebida via props do App.js)
                               quando o boto  clicado.
            - aria-label: Descreve a ao do boto para leitores de tela. */}
        <button onClick={onBack} className={styles.backButton} aria-label="Voltar para a lista de animais">
           Voltar  Lista
        </button>

        {/* Cabealho da Pgina de Detalhes:
            Contm o tazo que vira, nome do animal, status de extino.
            A cor da borda inferior  definida dinamicamente via 'style' prop,
            usando a cor hexadecimal do status de extino do animal. */}
        <header
          className={styles.detailHeader}
          style={{
            borderBottomColor: corDoStatusHex,
            borderBottomWidth: "5px",
            borderBottomStyle: "solid",
          }}
        >
          {/* Lgica para exibir o Tazo Interativo ou uma Imagem de Fallback */}
          {(tazoFrontUrl || tazoBackUrl) ? ( // Se existir imagem para frente OU verso do tazo...
            // Container do Tazo Interativo
            <div
              className={styles.headerTazoContainer} // Estilo base do container do tazo.
              // Eventos para controlar o estado 'tazoCabecalhoVirado':
              onMouseEnter={() => definirTazoCabecalhoVirado(true)} // Vira ao passar o mouse.
              onMouseLeave={() => definirTazoCabecalhoVirado(false)} // Desvira ao sair o mouse.
              onFocus={() => definirTazoCabecalhoVirado(true)} // Vira ao focar com Tab.
              onBlur={() => definirTazoCabecalhoVirado(false)} // Desvira ao perder o foco.
              // Eventos para abrir a imagem do tazo no lightbox:
              onClick={(evento) => {
                evento.stopPropagation(); // Evita que o clique propague para o <article> e feche o detalhe.
                const imagemAtualDoTazo = tazoCabecalhoVirado ? tazoBackUrl : tazoFrontUrl;
                if (imagemAtualDoTazo) {
                  abrirImagemNoLightbox(imagemAtualDoTazo);
                }
              }}
              onKeyDown={(evento) => {
                if (evento.key === 'Enter' || evento.key === ' ') {
                  evento.preventDefault(); // Previne comportamento padro (ex: rolar com Espao).
                  evento.stopPropagation();
                  const imagemAtualDoTazo = tazoCabecalhoVirado ? tazoBackUrl : tazoFrontUrl;
                  if (imagemAtualDoTazo) {
                    abrirImagemNoLightbox(imagemAtualDoTazo);
                  }
                }
              }}
              tabIndex="0" // Torna o tazo focvel.
              role="button" // Indica que  interativo como um boto.
              aria-label={`Tazo de ${nomeComumPrincipal}, atualmente ${tazoCabecalhoVirado ? 'mostrando o verso' : 'mostrando a frente'}. Pressione Enter ou Espao para ampliar a imagem visvel.`}
              aria-pressed={tazoCabecalhoVirado} // Indica se o estado "virado" est ativo.
            >
              {/* Container Interno que efetivamente gira */}
              <div
                className={`${styles.headerTazoInner} ${
                  tazoCabecalhoVirado ? styles.headerTazoInnerFlipped : "" // Aplica a classe de giro se 'tazoCabecalhoVirado' for true.
                }`}
              >
                {/* Face Frontal do Tazo */}
                <div className={`${styles.headerTazoFace} ${styles.headerTazoFront}`}>
                  {tazoFrontUrl ? (
                    <img src={tazoFrontUrl} alt="" loading="lazy" onError={(e) => e.target.style.display = 'none'} />
                  ) : (
                    tazoBackUrl && <div className={styles.imagePlaceholder}>Frente do Tazo Indisponvel</div>
                  )}
                </div>
                {/* Face do Verso do Tazo */}
                <div className={`${styles.headerTazoFace} ${styles.headerTazoBack}`}>
                  {tazoBackUrl ? (
                    <img src={tazoBackUrl} alt="" loading="lazy" onError={(e) => e.target.style.display = 'none'}/>
                  ) : (
                     tazoFrontUrl && <div className={styles.imagePlaceholder}>Verso do Tazo Indisponvel</div>
                  )}
                </div>
              </div>
            </div>
          ) : imagensDaGaleria.length > 0 ? ( // SENO, se no houver tazo mas houver imagens na galeria...
            // Mostra a primeira imagem da galeria como imagem principal.
            <img
              src={imagensDaGaleria[0]}
              alt={`Foto principal de ${nomeComumPrincipal}`}
              className={`${styles.detailImageLarge} ${styles.galleryImageClickable}`}
              onClick={() => abrirImagemNoLightbox(imagensDaGaleria[0])}
              onError={(e) => { e.target.style.display = 'none'; }}
              loading="lazy"
              title="Clique para ampliar a imagem"
            />
          ) : ( // SENO, se no houver NENHUMA imagem (nem tazo, nem galeria)...
            // Mostra um placeholder genrico.
            <div className={styles.imagePlaceholder} style={{height: '200px', width: '100%', maxWidth: '300px', margin: '0 auto 1.5rem auto'}}>
              Nenhuma imagem principal disponvel para {nomeComumPrincipal}.
            </div>
          )}

          {/* Nome Comum e Cientfico do Animal */}
          <h1 className={styles.animalNameTitle}>
            {nomeComumPrincipal}
            <span className={styles.scientificNameDetail}>
              (<em>{nomeCientificoPrincipal}</em>)
            </span>
          </h1>

          {/* Banner com o Status de Extino */}
          <div className={styles.extinctionBanner} style={{ backgroundColor: corDoStatusHex }}>
            <h2>{categoriaExtincao} - {descricaoExtincao}</h2>
          </div>

          {/* Escala Visual dos Nveis de Extino */}
          {/* S renderiza a escala se 'escalaExtincao' for um array com itens. */}
          {escalaExtincao.length > 0 && (
            <div className={styles.extinctionScaleVisual}>
              {escalaExtincao.map((nivel) => (
                <div
                  key={get(nivel, "sigla", Math.random())} // Usa sigla como key, ou fallback aleatrio se sigla faltar.
                  className={`${styles.scaleLevel} ${
                    get(nivel, "sigla") === categoriaExtincao ? styles.scaleLevelCurrent : "" // Destaca o nvel atual.
                  }`}
                  style={{ backgroundColor: getExtinctionHexColor(get(nivel, "sigla")) }} // Cor de fundo para cada nvel.
                  title={get(nivel, "descricao")} // Tooltip com a descrio completa do nvel.
                >
                  {get(nivel, "sigla", "?")} {/* Sigla do nvel (ex: LC, EN), ou "?" se faltar. */}
                </div>
              ))}
            </div>
          )}
        </header> {/* Fim do .detailHeader */}


        {/* --- SEES DE CONTEDO DETALHADO --- */}
        {/* Cada seo abaixo usa o componente auxiliar 'DetailSectionContent'
            para manter um padro visual e de fallback para dados ausentes. */}

        <DetailSectionContent
          title="Outros Nomes Comuns"
          icon="" // Emoji de "cabea falante"
          condition={Object.keys(nomesComunsParaExibicao).length > 0} // S mostra se houver nomes em outros idiomas.
        >
          {renderizarListaDeDefinicaoDetalhada(nomesComunsParaExibicao)}
        </DetailSectionContent>

        <DetailSectionContent
          title="Galeria de Fotos Adicionais"
          icon="" // Emoji de "moldura com paisagem"
          condition={imagensDaGaleria.length > 0} // S mostra se houver imagens na galeria.
        >
          <p className={styles.galleryHint}>
            Clique nas imagens abaixo para ampliar e navegar na galeria.
          </p>
          <div className={styles.galleryContainer}>
            {imagensDaGaleria.map((urlDaImagem, indice) => (
              <img
                key={`${urlDaImagem}-${indice}`} // Chave nica para cada imagem.
                src={urlDaImagem}
                alt={`Foto ${indice + 1} de ${nomeComumPrincipal}`} // Texto alternativo descritivo.
                className={`${styles.galleryImage} ${styles.galleryImageClickable}`} // Classes para estilo e interatividade.
                onClick={() => abrirImagemNoLightbox(urlDaImagem)} // Ao de clique para abrir no lightbox.
                onError={(e) => { e.target.style.display = 'none'; }} // Oculta se a imagem falhar.
                loading="lazy" // Carregamento preguioso.
                title="Clique para ampliar esta imagem" // Dica ao passar o mouse.
              />
            ))}
          </div>
        </DetailSectionContent>

        <DetailSectionContent title="Classificao Cientfica (Taxonomia)" icon="">
          {renderizarListaDeDefinicaoDetalhada(get(animal, "classificacao_taxonomica", {}))}
        </DetailSectionContent>

        <DetailSectionContent title="Detalhes do Status de Conservao" icon="">
          <>
            {get(animal, "status_conservacao.categoria_global", "N/A") !== "N/A" && (
              <p><strong>Categoria Global (IUCN):</strong> {get(animal, "status_conservacao.categoria_global")}</p>
            )}
            {get(animal, "status_conservacao.tendencia_populacional", "N/A") !== "N/A" && (
              <p style={{ marginTop: "0.3rem" }}><strong>Tendncia da Populao:</strong> {get(animal, "status_conservacao.tendencia_populacional")}</p>
            )}
            {get(animal, "status_conservacao.justificativa", "N/A") !== "N/A" && (
              <p className={styles.paragraphWithMargin} style={{ marginTop: "0.3rem" }}><strong>Justificativa do Status:</strong> {get(animal, "status_conservacao.justificativa")}</p>
            )}
            {/* Se todas as informaes desta seo estiverem ausentes, o DetailSectionContent mostrar a mensagem padro. */}
          </>
        </DetailSectionContent>

        {/* O Componente de Mapa, que recebe o objeto 'animal' para exibir sua distribuio. */}
        <MapComponent animal={animal} />

        <DetailSectionContent title="Habitat e Ecossistemas" icon="">
          <>
            {get(animal, "habitat.descricao", "N/A") !== "N/A" && (
              <p className={styles.paragraphWithMargin}>{get(animal, "habitat.descricao")}</p>
            )}
            {/* Verifica se 'habitats_principais'  um array e tem itens antes de tentar renderizar. */}
            {Array.isArray(get(animal, "habitat.habitats_principais", [])) && get(animal, "habitat.habitats_principais", []).length > 0 && (
              <>
                <h3 className={styles.sectionSubtitle}>Principais Tipos de Habitat:</h3>
                {renderizarListaDeItensSimples(get(animal, "habitat.habitats_principais", []), "")}
              </>
            )}
            {/* Se no houver descrio nem habitats principais, DetailSectionContent mostrar "Informao no disponvel". */}
          </>
        </DetailSectionContent>

        <DetailSectionContent title="Ecologia e Comportamento" icon="">
          {renderizarListaDeDefinicaoDetalhada(get(animal, "ecologia", {}))}
        </DetailSectionContent>

        <DetailSectionContent title="Principais Ameaas" icon="">
          {renderizarListaDeItensSimples(get(animal, "ameacas", []), "")}
        </DetailSectionContent>

        <DetailSectionContent title="Aes e Esforos de Conservao" icon="">
          <>
            {get(animal, "acoes_conservacao.protecao_legal", "N/A") !== "N/A" && (
                <p><strong>Medidas de Proteo Legal:</strong> {get(animal, "acoes_conservacao.protecao_legal")}</p>
            )}
            {Array.isArray(get(animal, "acoes_conservacao.acoes_recomendadas", [])) && get(animal, "acoes_conservacao.acoes_recomendadas", []).length > 0 && (
              <>
                <h3 className={styles.sectionSubtitle} style={{marginTop: get(animal, "acoes_conservacao.protecao_legal", "N/A") !== "N/A" ? '1rem' : '0'}}>
                  Aes Recomendadas para Conservao:
                </h3>
                {renderizarListaDeItensSimples(get(animal, "acoes_conservacao.acoes_recomendadas", []), "")}
              </>
            )}
          </>
        </DetailSectionContent>

        <DetailSectionContent title="Curiosidades e Fatos Interessantes" icon="">
          {renderizarListaDeItensSimples(get(animal, "curiosidades", []), "")}
        </DetailSectionContent>

        <DetailSectionContent title="Fonte das Informaes" icon="">
          <div className={styles.sourceSection}>
            {get(animal, "fonte.citacao", "N/A") !== "N/A" && (
              <p><strong>Citao:</strong> {get(animal, "fonte.citacao")}</p>
            )}
            {/* Verifica se o link existe e no  apenas "N/A" */}
            {get(animal, "fonte.link") && get(animal, "fonte.link") !== "N/A" && (
              <p>
                <strong>Link da Fonte:</strong>{" "}
                <a href={get(animal, "fonte.link")} target="_blank" rel="noopener noreferrer">
                  Visitar fonte original <span aria-label="(abre em nova aba)"></span>
                </a>
              </p>
            )}
          </div>
        </DetailSectionContent>
      </article> {/* Fim do .detailContainer */}


      {/* --- SEO DO LIGHTBOX (VISUALIZADOR DE IMAGEM AMPLIADA) --- */}
      {/* O lightbox s  renderizado se 'lightboxInfo.index' for diferente de -1
          E se houver imagens na lista 'lightboxInfo.images'. */}
      {lightboxInfo.index !== -1 && lightboxInfo.images.length > 0 && (
        // O backdrop  o fundo escuro que cobre a tela.
        // Clicar no backdrop chama 'fecharLightbox'.
        <div
          className={styles.lightboxBackdrop}
          onClick={fecharLightbox}
          role="dialog" // Semntica para leitores de tela.
          aria-modal="true" // Indica que o contedo por trs est inativo.
          aria-label={`Visualizador de imagem: ${nomeComumPrincipal}`} // Um rtulo geral para o dilogo.
        >
          {/* Wrapper do contedo do lightbox.
              Clicar DENTRO deste wrapper NO fecha o lightbox, graas a e.stopPropagation(). */}
          <div
            className={styles.lightboxContentWrapper}
            onClick={(e) => e.stopPropagation()} // Impede que o clique se propague para o backdrop.
          >
            {/* Boto "Anterior" (s aparece se houver mais de uma imagem para navegar) */}
            {lightboxInfo.images.length > 1 && (
              <button
                onClick={imagemAnteriorLightbox}
                className={`${styles.lightboxNavButton} ${styles.lightboxPrevButton}`}
                aria-label="Ver imagem anterior na galeria"
                title="Anterior (Seta Esquerda)"
              >
                &#10094; {/* Cdigo HTML para seta esquerda () */}
              </button>
            )}

            {/* Container da imagem ampliada */}
            <div className={styles.lightboxImageContainer}>
              <img
                src={lightboxInfo.images[lightboxInfo.index]} // A URL da imagem atual a ser exibida.
                alt={`Imagem ampliada ${lightboxInfo.index + 1} de ${lightboxInfo.images.length} do animal ${nomeComumPrincipal}`}
                className={styles.lightboxImage}
              />
            </div>

            {/* Boto "Prxima" (s aparece se houver mais de uma imagem) */}
            {lightboxInfo.images.length > 1 && (
              <button
                onClick={proximaImagemLightbox}
                className={`${styles.lightboxNavButton} ${styles.lightboxNextButton}`}
                aria-label="Ver prxima imagem na galeria"
                title="Prxima (Seta Direita)"
              >
                &#10095; {/* Cdigo HTML para seta direita () */}
              </button>
            )}

            {/* Boto para Fechar o Lightbox */}
            <button
              onClick={fecharLightbox}
              className={styles.lightboxCloseButton}
              aria-label="Fechar visualizador de imagem (Esc)"
              title="Fechar (Esc)"
            >
              &times; {/* Cdigo HTML para o smbolo de multiplicao (X) */}
            </button>
          </div> {/* Fim do .lightboxContentWrapper */}
        </div> // Fim do .lightboxBackdrop
      )} {/* Fim da renderizao condicional do Lightbox */}
    </> // Fim do Fragmento React
  );
}; // Fim do componente AnimalDetail

// Exporta o componente AnimalDetail para que possa ser usado no App.js.
export default AnimalDetail;