// Caminho: src/App.js

// =================================================================================================
// ARQUIVO: App.js
// PROPSITO: Componente principal (raiz) da aplicao ZooDex.
//            Responsvel por gerenciar o estado geral, incluindo a lista de animais,
//            filtros, ordenao, seleo de animal para detalhes, E AGORA, PAGINAO.
//            Ele orquestra a renderizao dos componentes Header, AnimalList,
//            AnimalDetail, Pagination e Footer.
//
// NOVOS CONCEITOS COM A PAGINAO:
//  - Mais Gerenciamento de Estado (useState):
//    - 'paginaAtual': Para saber qual pgina de resultados mostrar.
//    - 'itensPorPagina': Para definir quantos animais mostrar por pgina.
//  - Lgica de "Fatiamento" (Slicing) de Arrays: Para pegar apenas o subconjunto
//    de animais que pertence  pgina atual.
//  - Clculo de Total de Pginas: Para saber quantos botes de pgina gerar.
//  - Novas Funes de Callback: Para permitir que o componente de Paginao
//    altere a 'paginaAtual' no App.js.
// =================================================================================================

// --- 1. IMPORTAES ---
import React, { useState, useEffect, useCallback } from "react";
import { processedAnimalData, get } from "./data/animalData"; // Usaremos os dados completos agora.
import Header from "./components/Header";
import AnimalList from "./components/AnimalList";
import AnimalDetail from "./components/AnimalDetail";
import Footer from "./components/Footer";

// --- NOVO COMPONENTE (Placeholder): Pagination ---
// Idealmente, este seria um arquivo separado (src/components/Pagination.jsx).
// Por enquanto, vamos definir uma estrutura bsica aqui para que o App.js funcione.
// Voc pode me pedir para detalhar o Pagination.jsx e seu CSS depois.
const Pagination = ({ paginaAtual, totalDePaginas, onPageChange }) => {
  if (totalDePaginas <= 1) {
    // No mostra paginao se houver apenas uma pgina ou menos.
    return null;
  }

  const numerosDasPaginas = [];
  for (let i = 1; i <= totalDePaginas; i++) {
    numerosDasPaginas.push(i);
  }

  // Estilos inline MUITO bsicos apenas para este exemplo.
  // No componente real, usaramos CSS Modules.
  const estiloBotao = {
    margin: "0 5px",
    padding: "5px 10px",
    cursor: "pointer",
    border: "1px solid var(--color-primary)",
    borderRadius: "var(--border-radius-small)",
  };
  const estiloBotaoAtivo = {
    ...estiloBotao,
    backgroundColor: "var(--color-primary)",
    color: "white",
    fontWeight: "bold",
  };

  return (
    <nav
      aria-label="Navegao de pginas de animais"
      style={{ textAlign: "center", margin: "2rem 0" }}
    >
      <button
        onClick={() => onPageChange(paginaAtual - 1)}
        disabled={paginaAtual === 1} // Desabilita se estiver na primeira pgina.
        style={estiloBotao}
        aria-label="Ir para a pgina anterior"
      >
        Anterior
      </button>
      {numerosDasPaginas.map((numero) => (
        <button
          key={numero}
          onClick={() => onPageChange(numero)}
          disabled={paginaAtual === numero} // Desabilita o boto da pgina atual.
          style={paginaAtual === numero ? estiloBotaoAtivo : estiloBotao}
          aria-current={paginaAtual === numero ? "page" : undefined} // Acessibilidade: indica a pgina atual.
          aria-label={`Ir para a pgina ${numero}`}
        >
          {numero}
        </button>
      ))}
      <button
        onClick={() => onPageChange(paginaAtual + 1)}
        disabled={paginaAtual === totalDePaginas} // Desabilita se estiver na ltima pgina.
        style={estiloBotao}
        aria-label="Ir para a prxima pgina"
      >
        Prxima
      </button>
    </nav>
  );
};

// --- 2. DEFINIO DO COMPONENTE FUNCIONAL 'App' ---
function App() {
  // --- A. ESTADOS DO COMPONENTE ---

  // Estado para a LISTA COMPLETA de animais aps filtro e ordenao.
  // Esta lista ser ento "fatiada" para a paginao.
  const [animaisFiltradosEOrdenados, definirAnimaisFiltradosEOrdenados] =
    useState(processedAnimalData);

  // Estado para a LISTA DE ANIMAIS A SEREM EFETIVAMENTE EXIBIDOS NA PGINA ATUAL.
  // Este  o array que ser passado para o componente AnimalList.
  const [animaisParaExibirNaPagina, definirAnimaisParaExibirNaPagina] =
    useState([]);

  const [idAnimalSelecionado, definirIdAnimalSelecionado] = useState(null);
  const [termoDeBusca, definirTermoDeBusca] = useState("");
  const [ordenacaoAtual, definirOrdenacaoAtual] = useState("codigo-asc"); // Padro

  // NOVOS ESTADOS PARA PAGINAO:
  const [paginaAtual, definirPaginaAtual] = useState(1); // A paginao comea na pgina 1.
  const [itensPorPagina] = useState(8); // Quantos animais mostrar por pgina.
  // Poderia ser um estado se voc quisesse permitir
  // que o usurio mudasse isso (ex: via um dropdown).
  // Para simplificar, vamos manter como uma constante por enquanto.

  // Estado para o NMERO TOTAL DE PGINAS.
  // Ser calculado com base no total de animais e itensPorPagina.
  const [totalDePaginas, definirTotalDePaginas] = useState(0);

  // --- B. EFEITO PARA FILTRAR E ORDENAR A LISTA COMPLETA DE ANIMAIS ---
  // Este useEffect reage a mudanas no 'termoDeBusca' ou 'ordenacaoAtual'.
  // Seu resultado (uma lista filtrada e ordenada) ser armazenado em 'animaisFiltradosEOrdenados'.
  useEffect(() => {
    // console.log("App.js - useEffect [Filtro/Ordenao]: Disparado.");
    let animaisProcessados = [...processedAnimalData]; // Comea com todos os animais.

    // 1. APLICAR FILTRO DE BUSCA
    if (termoDeBusca && termoDeBusca.trim() !== "") {
      const termoMinusculo = termoDeBusca.toLowerCase().trim();
      animaisProcessados = animaisProcessados.filter(
        (animal) =>
          get(animal, "nome_tazo", "").toLowerCase().includes(termoMinusculo) ||
          get(animal, "nome_cientifico", "")
            .toLowerCase()
            .includes(termoMinusculo)
      );
    }

    // 2. APLICAR ORDENAO
    if (ordenacaoAtual) {
      // A lgica de switch para ordenao permanece a mesma da verso anterior.
      switch (ordenacaoAtual) {
        case "name-asc":
          animaisProcessados.sort((a, b) =>
            get(a, "nome_tazo", "").localeCompare(get(b, "nome_tazo", ""))
          );
          break;
        case "name-desc":
          animaisProcessados.sort((a, b) =>
            get(b, "nome_tazo", "").localeCompare(get(a, "nome_tazo", ""))
          );
          break;
        case "risk-desc":
          animaisProcessados.sort(
            (a, b) =>
              get(b, "nivel_extincao.indice_risco_calculado", 0) -
              get(a, "nivel_extincao.indice_risco_calculado", 0)
          );
          break;
        case "risk-asc":
          animaisProcessados.sort(
            (a, b) =>
              get(a, "nivel_extincao.indice_risco_calculado", 0) -
              get(b, "nivel_extincao.indice_risco_calculado", 0)
          );
          break;
        case "codigo-asc":
          animaisProcessados.sort(
            (a, b) => get(a, "codigo", 0) - get(b, "codigo", 0)
          );
          break;
        case "codigo-desc":
          animaisProcessados.sort(
            (a, b) => get(b, "codigo", 0) - get(a, "codigo", 0)
          );
          break;
        default:
          // Se um tipo de ordenao desconhecido for passado, podemos logar um aviso
          // e talvez manter a ordem atual ou aplicar uma ordenao padro.
          console.warn(
            `App.js: Tipo de ordenao desconhecido "${ordenacaoAtual}". Mantendo ordenao por cdigo ascendente.`
          );
          animaisProcessados.sort(
            (a, b) => get(a, "codigo", 0) - get(b, "codigo", 0)
          );
          break;
      }
    }

    // Define o estado com a lista completa j filtrada e ordenada.
    definirAnimaisFiltradosEOrdenados(animaisProcessados);

    // IMPORTANTE PARA PAGINAO: Quando a lista base (filtrada/ordenada) muda,
    // devemos VOLTAR para a primeira pgina para evitar exibir uma pgina vazia
    // ou uma pgina que no faz mais sentido com os novos dados.
    definirPaginaAtual(1);

    // Array de dependncias: este efeito roda se 'termoDeBusca' ou 'ordenacaoAtual' mudarem.
    // 'processedAnimalData'  includo caso, hipoteticamente, a fonte de dados original pudesse mudar.
    // Como  uma constante importada, no mudar, mas  uma boa prtica considerar se fosse dinmico.
  }, [termoDeBusca, ordenacaoAtual, processedAnimalData]);

  // --- C. EFEITO PARA ATUALIZAR OS ITENS DA PGINA ATUAL E O TOTAL DE PGINAS ---
  // Este useEffect reage a mudanas na lista 'animaisFiltradosEOrdenados' (resultado do efeito anterior)
  // OU na 'paginaAtual' OU em 'itensPorPagina'.
  // Ele  responsvel por:
  //   1. Calcular o subconjunto (fatia) de animais que deve ser exibido na pgina atual.
  //   2. Calcular e definir o nmero total de pginas.
  useEffect(() => {
    // console.log(`App.js - useEffect [Paginao]: Disparado. Pgina Atual: ${paginaAtual}`);

    // 1. CALCULAR O TOTAL DE PGINAS:
    //    - 'animaisFiltradosEOrdenados.length': O nmero total de animais aps filtro/ordenao.
    //    - 'itensPorPagina': Quantos animais mostramos por pgina.
    //    - 'Math.ceil()': Arredonda para o nmero inteiro MAIS PRXIMO ACIMA.
    //                     Ex: Se temos 25 animais e 10 por pgina, 25/10 = 2.5. Math.ceil(2.5) = 3 pginas.
    const novoTotalDePaginas = Math.ceil(
      animaisFiltradosEOrdenados.length / itensPorPagina
    );
    definirTotalDePaginas(novoTotalDePaginas);

    // 2. CALCULAR OS NDICES PARA "FATIAR" O ARRAY:
    //    O mtodo '.slice(inicio, fim)' de arrays em JavaScript retorna uma cpia
    //    rasa de uma poro do array, do ndice 'inicio' AT (mas no incluindo) o ndice 'fim'.
    //    Os ndices de array comeam em 0.
    //
    //    - 'indiceDeInicio': O ndice do primeiro animal da pgina atual.
    //                      Para a pgina 1, (1-1)*10 = 0.
    //                      Para a pgina 2, (2-1)*10 = 10.
    const indiceDeInicio = (paginaAtual - 1) * itensPorPagina;
    //    - 'indiceDeFim': O ndice do LTIMO animal da pgina atual + 1.
    //                     Para a pgina 1, 0 + 10 = 10. Slice pegar de 0 a 9.
    //                     Para a pgina 2, 10 + 10 = 20. Slice pegar de 10 a 19.
    const indiceDeFim = indiceDeInicio + itensPorPagina;

    // 3. "FATIAR" O ARRAY PARA OBTER OS ANIMAIS DA PGINA ATUAL:
    const animaisDaPaginaAtual = animaisFiltradosEOrdenados.slice(
      indiceDeInicio,
      indiceDeFim
    );

    // 4. ATUALIZAR O ESTADO DOS ANIMAIS A SEREM EXIBIDOS:
    definirAnimaisParaExibirNaPagina(animaisDaPaginaAtual);

    // Array de dependncias: este efeito roda se a lista filtrada/ordenada mudar,
    // ou se a pgina atual mudar, ou se a quantidade de itens por pgina mudar.
  }, [animaisFiltradosEOrdenados, paginaAtual, itensPorPagina]);

  // --- D. FUNES DE CALLBACK MEMORIZADAS (useCallback) ---

  // Funo para lidar com a mudana de texto na busca.
  //  passada para o componente Header.
  const lidarComBusca = useCallback((termo) => {
    definirTermoDeBusca(termo); // Atualiza o estado do termo de busca.
    // Ao fazer uma nova busca,  importante resetar para a primeira pgina
    // para evitar que o usurio fique em uma pgina que no existe mais
    // para os novos resultados da busca.
    // A lgica de resetar a pgina j est no useEffect de filtro/ordenao.
    definirIdAnimalSelecionado(null); // Volta para a lista ao buscar.
  }, []); // Array de dependncias vazio, pois 'definirTermoDeBusca' e 'definirIdAnimalSelecionado' so estveis.

  // Funo para lidar com a mudana de critrio de ordenao.
  //  passada para o componente Header.
  const lidarComOrdenacao = useCallback((tipoDeOrdenacao) => {
    definirOrdenacaoAtual(tipoDeOrdenacao); // Atualiza o estado da ordenao.
    // Similar  busca, resetar para a primeira pgina ao reordenar.
    // A lgica de resetar a pgina j est no useEffect de filtro/ordenao.
    definirIdAnimalSelecionado(null); // Volta para a lista ao ordenar.
  }, []);

  // Funo para lidar com a seleo de um animal (clique no card).
  //  passada para AnimalList, que por sua vez a passa para cada AnimalCard.
  const lidarComSelecaoDeAnimal = useCallback((idDoAnimal) => {
    definirIdAnimalSelecionado(idDoAnimal); // Define qual animal est selecionado.
    window.scrollTo(0, 0); // Rola a pgina para o topo para mostrar o detalhe do animal.
  }, []);

  // Funo para voltar da tela de detalhes para a lista de animais.
  //  passada para o componente AnimalDetail.
  const lidarComVoltarParaLista = useCallback(() => {
    definirIdAnimalSelecionado(null); // Limpa o ID do animal selecionado, voltando para a exibio da lista.
  }, []);

  // Funo para selecionar um animal aleatoriamente.
  //  passada para o componente Header.
  const lidarComAnimalAleatorio = useCallback(() => {
    if (processedAnimalData && processedAnimalData.length > 0) {
      const indiceAleatorio = Math.floor(
        Math.random() * processedAnimalData.length
      );
      const animalAleatorioId = processedAnimalData[indiceAleatorio].id_animal;
      definirIdAnimalSelecionado(animalAleatorioId);
      window.scrollTo(0, 0);
    }
  }, [processedAnimalData]); // Depende de 'processedAnimalData' para saber de onde sortear.

  // NOVA FUNO DE CALLBACK PARA PAGINAO:
  // Esta funo ser chamada pelo componente Pagination quando o usurio clicar
  // em um boto de pgina (Anterior, Prxima, ou um nmero de pgina).
  // 'numeroDaPaginaAlvo'  o nmero da pgina para a qual queremos navegar.
  const lidarComMudancaDePagina = useCallback(
    (numeroDaPaginaAlvo) => {
      // console.log(`App.js: Mudando para a pgina ${numeroDaPaginaAlvo}`);

      // Validaes simples para garantir que o nmero da pgina alvo seja vlido.
      if (numeroDaPaginaAlvo >= 1 && numeroDaPaginaAlvo <= totalDePaginas) {
        definirPaginaAtual(numeroDaPaginaAlvo); // Atualiza o estado da pgina atual.
        window.scrollTo(0, 0); // Opcional: rolar para o topo ao mudar de pgina.
      } else {
        console.warn(
          `App.js: Tentativa de navegar para pgina invlida: ${numeroDaPaginaAlvo}. Total de pginas: ${totalDePaginas}`
        );
      }
    },
    [totalDePaginas]
  ); // Depende de 'totalDePaginas' para validao. 'definirPaginaAtual'  estvel.

  // --- E. LGICA PARA ENCONTRAR O ANIMAL SELECIONADO PARA DETALHE ---
  // Se 'idAnimalSelecionado' NO for null, procuramos o objeto completo do animal
  // na nossa lista 'processedAnimalData' original.
  const animalDetalhado = idAnimalSelecionado
    ? processedAnimalData.find(
        (animal) => animal.id_animal === idAnimalSelecionado
      )
    : null; // Se nenhum ID estiver selecionado, 'animalDetalhado' ser null.

  // --- F. RENDERIZAO DO COMPONENTE App ---
  return (
    // Fragmento React (<>...</>) para agrupar os elementos sem adicionar um n <div> extra ao DOM.
    <>
      {/* Renderiza o Header CONDICIONALMENTE:
          S mostra o Header se NO estivermos na tela de detalhes de um animal. */}
      {!animalDetalhado && (
        <Header
          onSearch={lidarComBusca}
          onSort={lidarComOrdenacao}
          onRandom={lidarComAnimalAleatorio}
          initialSearchTerm={termoDeBusca}
        />
      )}

      {/* Contedo Principal (<main>) */}
      <main
        style={{
          flexGrow: 1, // Para ocupar o espao vertical disponvel.
          padding: "var(--spacing-medium, 1rem)", // Usando varivel CSS.
          maxWidth: "1200px",
          width: "100%",
          marginLeft: "auto",
          marginRight: "auto",
        }}
      >
        {/* Lgica de Renderizao Condicional para o contedo principal: */}
        {animalDetalhado ? (
          // SE um animal estiver selecionado (animalDetalhado no  null):
          // Mostra o componente AnimalDetail com os dados desse animal.
          <AnimalDetail
            animal={animalDetalhado}
            onBack={lidarComVoltarParaLista}
          />
        ) : // SENO (nenhum animal selecionado, mostramos a lista):
        animaisParaExibirNaPagina.length > 0 ? (
          // SE houver animais para exibir na pgina atual (aps filtro e paginao):
          // Mostra o componente AnimalList.
          <>
            {" "}
            {/* Usamos um fragmento para agrupar AnimalList e Pagination */}
            <AnimalList
              animals={animaisParaExibirNaPagina} // Passa APENAS os animais da pgina atual.
              onSelectAnimal={lidarComSelecaoDeAnimal}
            />
            {/* Renderiza o componente de Paginao */}
            <Pagination
              paginaAtual={paginaAtual}
              totalDePaginas={totalDePaginas}
              onPageChange={lidarComMudancaDePagina} // Passa a funo para mudar de pgina.
            />
          </>
        ) : (
          // SENO (nenhum animal selecionado E a lista de animais para exibir est vazia,
          //        ex: resultado de uma busca sem correspondncias):
          // Mostra uma mensagem informativa.
          <p className="error-message">
            {" "}
            {/* Usando classe global de index.css */}
            Nenhum animal encontrado. Tente uma busca ou filtro diferente!
          </p>
        )}
      </main>

      {/* Renderiza o Footer no final da pgina. */}
      <Footer />
    </>
  );
}

// Exporta o componente App.
export default App;
