// --- FUNO UTILITRIA: get ---
/*
  Esta funo, chamada 'get',  uma forma segura de acessar propriedades aninhadas
  dentro de um objeto. Se alguma propriedade no caminho no existir, em vez de
  causar um erro na aplicao, ela retorna um valor padro (que por defeito  a string "N/A").

  Parmetros:
  - obj: O objeto do qual queremos ler uma propriedade.
  - path: Uma string indicando o caminho para a propriedade desejada,
          separado por pontos (ex: "habitat.descricao.tipo").
  - defaultValue: O valor a ser retornado se a propriedade no for encontrada ou for invlida.
                  O padro  "N/A".

  Exemplo de uso:
  const descricaoHabitat = get(animal, "habitat.descricao", "Descrio no informada");
*/
export const get = (obj, path, defaultValue = "N/A") => {
  // 'path.split(".")' transforma a string do caminho (ex: "a.b.c") em um array (ex: ["a", "b", "c"]).
  // 'reduce()'  um mtodo de array que executa uma funo para cada elemento do array,
  // resultando em um nico valor de retorno.
  // Aqui, ele tenta navegar pelo objeto 'obj' usando as partes do caminho.
  // 'acc'  o acumulador (o objeto atual no caminho), 'part'  a parte atual do caminho (a chave).
  const value = path.split(".").reduce((acc, part) => {
    // Se 'acc' (o objeto atual) existir e tiver a propriedade 'part', retorna o valor dessa propriedade.
    // Seno, retorna 'undefined', interrompendo a navegao.
    return acc && acc[part] !== undefined ? acc[part] : undefined;
  }, obj);

  // Verificaes para considerar um valor como "no encontrado" ou "invlido":
  if (
    value === undefined || // Se o valor for explicitamente undefined
    value === null || // Se o valor for null
    (typeof value === "string" && value.trim() === "") || // Se for uma string vazia ou s com espaos
    (Array.isArray(value) &&
      value.length === 0 &&
      !path.endsWith("nivel_extincao.escala")) // Se for um array vazio (exceto para a escala de extino)
  ) {
    // Casos especiais podem ser adicionados aqui se necessrio.
    // Por exemplo, se 'nivel_extincao.escala' puder ser um array vazio intencionalmente,
    // no deveramos retornar defaultValue.
    // A condio '!path.endsWith("nivel_extincao.escala")' foi adicionada para
    // tratar o caso onde 'nivel_extincao.escala'  um array que DEVE ser retornado mesmo se vazio,
    // mas foi removida do if original para simplificar e garantir que a escala seja sempre tratada.
    // Re-avaliando a lgica original, a escala  tratada de forma diferente.
    return defaultValue;
  }

  // Caso especfico para a escala de extino: se for um array de objetos vlidos, retorna o array.
  // Isso garante que a estrutura da escala seja mantida, mesmo que precise ser filtrada ou validada depois.
  if (
    Array.isArray(value) &&
    path.endsWith("nivel_extincao.escala") && // Verifica se estamos acessando especificamente a escala
    value.length > 0 &&
    value.every(
      (item) =>
        typeof item === "object" &&
        item !== null &&
        item.sigla &&
        item.descricao
    )
  ) {
    return value;
  }
  // Se for um array vazio e no for a escala de extino (ou se a escala for vazia e quisermos o default).
  // A lgica original tinha uma verificao especfica para arrays vazios que no eram a escala.
  if (Array.isArray(value) && value.length === 0) return defaultValue;

  // Se o valor for encontrado e vlido, retorna o valor.
  return value;
};

// --- PROCESSAMENTO INICIAL DOS DADOS ---
/*
  A constante 'processedAnimalData' pega o array 'allAnimalDataRaw' e faz alguns
  processamentos e adies em cada objeto de animal.
  Isso  feito para garantir que cada animal tenha um 'id_animal' nico e um
  'indice_risco_calculado' consistente.
*/
export const processedAnimalData = allAnimalDataRaw
  .map((animalData) => {
    // Mapeia cada animal do array original para um novo objeto animal processado.

    // 1. Gerar um 'id_animal' nico:
    //    - Tenta usar 'animalData.codigo' se existir.
    //    - Seno, tenta criar um ID a partir do 'nome_cientifico' (minsculas, espaos trocados por '_').
    //    - Seno (como ltimo recurso), gera um ID aleatrio.
    //    O 'String()' garante que o ID seja sempre uma string.
    const id_animal = String(
      animalData.codigo ||
        get(animalData, "nome_cientifico", "")
          .toLowerCase()
          .replace(/\s+/g, "_") ||
        `animal_${Math.random().toString(36).substring(2, 9)}` // Gera uma string aleatria
    );

    // 2. Calcular o 'indice_risco_calculado':
    //    Este mapa converte a sigla da categoria de extino (ex: "LC", "EN") para um nmero.
    //    Isso  til para ordenar os animais por nvel de risco.
    const riskMap = {
      LC: 1,
      NT: 2,
      VU: 3,
      EN: 4,
      CR: 5,
      EW: 6,
      EX: 7,
      DD: 0, // Dados Insuficientes
      NE: 0, // No Avaliado
    };
    // Pega a categoria de extino do animal (ex: "LC"), converte para maisculas.
    const categoriaExtincao = get(
      animalData,
      "nivel_extincao.categoria",
      ""
    ).toUpperCase();
    // Busca o ndice de risco no 'riskMap'. Se no encontrar, usa o 'indice_risco' original
    // do animal, ou 0 como padro.
    const indiceRiscoCalculado =
      riskMap[categoriaExtincao] ??
      get(animalData, "nivel_extincao.indice_risco", 0);

    // Retorna um novo objeto para o animal, incluindo todas as propriedades originais (...animalData)
    // e as novas propriedades calculadas (id_animal, nivel_extincao com indice_risco_calculado).
    return {
      ...animalData, // Espalha todas as propriedades originais do animal.
      id_animal, // Adiciona o ID nico gerado.
      nivel_extincao: {
        ...(animalData.nivel_extincao || {}), // Espalha as propriedades originais de nivel_extincao (se existirem).
        indice_risco_calculado: indiceRiscoCalculado, // Adiciona o ndice de risco calculado.
        // Garante que a propriedade 'escala' seja sempre um array, mesmo que os dados originais
        // no a forneam ou a forneam incorretamente. Usa a funo 'get' para buscar a escala
        // e verifica se  um array. Se no for, define como um array vazio.
        escala: Array.isArray(get(animalData, "nivel_extincao.escala", []))
          ? get(animalData, "nivel_extincao.escala", [])
          : [],
      },
    };
  })
  .filter((animalData) => animalData.id_animal); // Remove quaisquer animais que, por algum motivo, no tenham um id_animal.

// --- FUNES PARA CORES DE EXTINO (USANDO VALORES HEXADECIMAIS) ---
/*
  Esta funo retorna o valor hexadecimal da cor correspondente
   categoria de extino.
  til para aplicar estilos inline diretamente em elementos JSX,
  ou para usar em lgicas que no dependem do Tailwind.

  Parmetro:
  - category: A sigla da categoria de extino (ex: "LC", "EN").

  Exemplo de uso no JSX para estilo inline:
  <div style={{ backgroundColor: getExtinctionHexColor(animal.nivel_extincao.categoria) }}>
    Status
  </div>
*/
export const getExtinctionHexColor = (category) => {
  const themeColors = {
    lc: "#AED581", // Verde claro para "Pouco Preocupante"
    nt: "#DCE775", // Amarelo-esverdeado para "Quase Ameaado"
    vu: "#FFEE58", // Amarelo para "Vulnervel"
    en: "#FFA726", // Laranja para "Em Perigo"
    cr: "#EF5350", // Vermelho para "Criticamente em Perigo"
    ew: "#7E57C2", // Roxo para "Extinto na Natureza"
    ex: "#616161", // Cinza escuro para "Extinto"
    dd: "#BDBDBD", // Cinza mdio para "Dados Insuficientes"
    ne: "#E0E0E0", // Cinza claro para "No Avaliado"
    "default-extinction": "#CCCCCC", // Cinza padro para categorias desconhecidas
  };
  // Retorna a cor hexadecimal correspondente, convertendo a categoria para minsculas.
  // Se a categoria no for encontrada, retorna a cor padro.
  return (
    themeColors[String(category).toLowerCase()] ||
    themeColors["default-extinction"]
  );
};

/*
  Consideraes para os alunos:
  - Estrutura de Dados: Observar como os dados de cada animal so organizados em objetos com propriedades aninhadas.
  - Arrays de Objetos: 'allAnimalDataRaw'  um array, e cada item  um objeto animal.
  - Funes Utilitrias:
    - A funo 'get'  uma ferramenta poderosa e segura para acessar dados em JavaScript, evitando erros comuns.
    - O processamento em 'processedAnimalData' adiciona informaes teis (como 'id_animal' e 'indice_risco_calculado')
      que facilitam o uso dos dados nos componentes React.
    - As funes de cor ajudam a manter a consistncia visual relacionada ao status de extino.
  - Exportao: As palavras-chave 'export' tornam as constantes e funes deste arquivo
    disponveis para serem importadas em outros arquivos (como nos componentes React).
*/
