// Caminho: src/components/Header.jsx

// =================================================================================================
// ARQUIVO: Header.jsx
// PROPSITO: Este componente define o Cabealho (Header) da aplicao ZooDex.
//            O cabealho  a barra no topo da pgina e contm:
//            - O logotipo e o ttulo da aplicao.
//            - Um boto para selecionar um "Animal Surpresa".
//            - Um campo de texto para o usurio buscar animais por nome.
//            - Botes para o usurio ordenar a lista de animais.
//
// CONCEITOS PRINCIPAIS DO REACT E JAVASCRIPT UTILIZADOS AQUI:
//  - Componentes Funcionais: Como este componente  estruturado.
//  - Props (Propriedades): Como o Header recebe dados e, mais importante, FUNES
//                          de seu componente pai (App.js). Essas funes (callbacks)
//                          permitem que o Header comunique ao App.js as aes do usurio
//                          (ex: o que foi digitado na busca, qual boto de ordenao foi clicado).
//                          As props recebidas so:
//                            - 'onSearch': Funo a ser chamada quando o texto na busca muda.
//                            - 'onSort': Funo a ser chamada quando um boto de ordenao  clicado.
//                            - 'onRandom': Funo a ser chamada quando o boto "Animal Surpresa"  clicado.
//                            - 'initialSearchTerm': O valor inicial para o campo de busca (til se o App.js
//                                                   quiser restaurar um termo de busca anterior).
//  - useState: Hook do React para adicionar ESTADO LOCAL ao componente Header.
//              Neste caso, usamos para controlar o valor ATUAL do campo de busca.
//              O campo de busca se torna um "componente controlado" pelo React.
//  - JSX: A sintaxe para descrever a estrutura da interface do usurio.
//  - Manipulao de Eventos: Como o componente reage a aes do usurio, como:
//                           - 'onChange' no campo de busca (quando o usurio digita).
//                           - 'onClick' nos botes.
//  - Acessibilidade: Uso de 'aria-label' e 'title' para tornar a interface mais
//                    acessvel para usurios com tecnologias assistivas.
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS E FUNES ---

// Importamos a biblioteca 'React' e o Hook 'useState'.
// - React: Essencial para criar o componente e usar JSX.
// - useState: Permite que o componente Header tenha seu prprio estado interno.
//           Neste caso, o estado ser o texto atual dentro do campo de busca.
import React, { useState } from "react";

// Importamos o objeto 'styles' do nosso arquivo CSS Module (Header.module.css).
// Isso nos d acesso s classes CSS definidas l de forma escopada,
// como 'styles.header', 'styles.searchInput', etc.
import styles from "./Header.module.css";

// --- 2. DEFINIO DO COMPONENTE FUNCIONAL 'Header' ---

// O componente Header  uma funo que aceita um objeto de 'props' como argumento.
// Usamos a desestruturao para pegar diretamente as props que esperamos receber do App.js:
//  - onSearch: Uma funo que o App.js nos passou. O Header vai cham-la sempre que
//              o usurio digitar algo no campo de busca.
//  - onSort: Uma funo do App.js. O Header vai cham-la quando um boto de ordenao
//            for clicado, informando qual tipo de ordenao foi escolhido.
//  - onRandom: Uma funo do App.js. O Header vai cham-la quando o boto "Animal Surpresa"
//              for clicado.
//  - initialSearchTerm: O texto que deve aparecer inicialmente no campo de busca.
//                       Se no for fornecido, o campo comear vazio.
const Header = ({ onSearch, onSort, onRandom, initialSearchTerm }) => {
  // --- A. ESTADO LOCAL DO COMPONENTE (para o campo de busca) ---
  // Usamos 'useState' para criar uma varivel de estado chamada 'termoDeBuscaDigitado'.
  // - 'termoDeBuscaDigitado': Armazena o texto ATUAL que est no campo de input da busca.
  // - 'definirTermoDeBuscaDigitado':  a FUNO que usamos para ATUALIZAR 'termoDeBuscaDigitado'.
  // - `useState(initialSearchTerm || "")`: O valor inicial de 'termoDeBuscaDigitado'.
  //   Se 'initialSearchTerm' foi passado como prop, usamos ele. Seno (||), usamos uma string vazia "".
  //   Isso faz com que o campo de busca seja um "componente controlado": seu valor 
  //   controlado pelo estado do React, e no diretamente pelo DOM.
  const [termoDeBuscaDigitado, definirTermoDeBuscaDigitado] = useState(
    initialSearchTerm || ""
  );

  // --- B. FUNO PARA LIDAR COM MUDANAS NO CAMPO DE BUSCA (EVENT HANDLER) ---
  // Esta funo ('lidarComMudancaNaBusca')  chamada TODA VEZ que o usurio digita algo
  // no campo de input da busca (devido ao atributo 'onChange' no <input />).
  // O 'evento'  um objeto que o navegador passa para a funo, contendo informaes
  // sobre o que aconteceu (neste caso, a mudana no input).
  const lidarComMudancaNaBusca = (evento) => {
    // 1. Pegar o novo valor do campo de busca:
    //    'evento.target' se refere ao elemento HTML <input /> que disparou o evento.
    //    'evento.target.value'  o texto atual DENTRO desse campo de input.
    const novoValorDoInput = evento.target.value;

    // 2. Atualizar o estado local do Header:
    //    Chamamos 'definirTermoDeBuscaDigitado' para atualizar nossa varivel de estado
    //    'termoDeBuscaDigitado' com o novo texto que o usurio digitou.
    //    Isso faz com que o React re-renderize o Header, e o <input /> mostrar o novo valor.
    definirTermoDeBuscaDigitado(novoValorDoInput);

    // 3. Comunicar a mudana para o componente pai (App.js):
    //    Verificamos se a funo 'onSearch' foi realmente passada como prop.
    if (onSearch) {
      // Se sim, chamamos 'onSearch', passando o 'novoValorDoInput'.
      // Desta forma, o componente App.js  NOTIFICADO sobre o que o usurio
      // digitou, e o App.js pode ento usar esse termo para filtrar a lista de animais.
      // Isso  um exemplo de "elevar o estado" (lifting state up) ou, mais precisamente,
      // de comunicao de filho para pai atravs de callbacks.
      onSearch(novoValorDoInput);
    }
  };

  // --- C. RETORNO DO JSX (O QUE O COMPONENTE Header VAI RENDERIZAR) ---
  // A tag <header> do HTML5  usada para o container principal do cabealho.
  return (
    <header className={styles.header}>
      {/* Wrapper para o contedo principal (logo, ttulo, boto surpresa)
          para ajudar no layout responsivo com Flexbox. */}
      <div className={styles.headerContentWrapper}>
        {/* Agrupa o logo e o ttulo. */}
        <div className={styles.logoAndTitle}>
          <img
            src="https://colegiouni.com.br/new_site/wp-content/uploads/2019/04/Logo-Col%C3%A9gio-Unilavras-Oficial_2019-full-white-1.png"
            alt="Logotipo do Colgio Unilavras. Um escudo azul com um globo terrestre estilizado e o nome Unilavras abaixo." // Texto alternativo bem descritivo.
            className={styles.logo}
          />
          <h1 className={styles.title}>ZooDex </h1>{" "}
          {/* Ttulo da aplicao. */}
        </div>

        {/* Boto "Animal Surpresa":
            - onClick={onRandom}: Quando clicado, chama a funo 'onRandom' que foi
                                 passada como prop pelo App.js. O App.js ento
                                 cuidar da lgica de selecionar um animal aleatoriamente.
            - title: Fornece uma dica visual ao passar o mouse sobre o boto. */}
        <button
          onClick={onRandom} // Chama a funo do App.js
          className={styles.randomButton}
          title="Clique para ver informaes de um animal escolhido aleatoriamente!"
        >
          Animal Surpresa 
        </button>
      </div>{" "}
      {/* Fim de .headerContentWrapper */}
      {/* Container para o campo de busca. */}
      <div className={styles.searchContainer}>
        <input
          type="search" // Tipo de input semanticamente correto para buscas.
          id="search-input" // ID para o input, pode ser til para labels ou testes.
          value={termoDeBuscaDigitado} // O valor do input  controlado pelo estado 'termoDeBuscaDigitado'.
          // Isso o torna um "componente controlado".
          onChange={lidarComMudancaNaBusca} // Funo chamada sempre que o valor do input muda.
          placeholder="Buscar por nome comum ou cientfico..." // Texto de ajuda dentro do campo.
          className={styles.searchInput} // Classe CSS para estilizao.
          aria-label="Campo de busca para encontrar animais por nome comum ou cientfico" // Descrio para leitores de tela.
        />
      </div>{" "}
      {/* Fim de .searchContainer */}
      {/* Container para os botes de ordenao. */}
      <div className={styles.sortControls}>
        {/*
          Cada boto de ordenao, quando clicado, chama a funo 'onSort' (passada pelo App.js),
          enviando uma string que identifica o tipo de ordenao desejado (ex: "name-asc").
          O App.js usar essa string para reordenar a lista de animais.
          Usamos arrow functions no onClick (ex: () => onSort("name-asc")) para poder passar
          um argumento especfico para 'onSort' quando o boto  clicado. Se fosse apenas
          onClick={onSort}, no poderamos passar o tipo de ordenao diretamente.
        */}
        <button
          onClick={() => onSort("name-asc")} // Ordena por nome, de A a Z.
          className={styles.sortButton}
          aria-label="Ordenar animais por nome em ordem alfabtica (A-Z)"
        >
          Nome (A-Z)
        </button>
        <button
          onClick={() => onSort("name-desc")} // Ordena por nome, de Z a A.
          className={styles.sortButton}
          aria-label="Ordenar animais por nome em ordem alfabtica inversa (Z-A)"
        >
          Nome (Z-A)
        </button>
        <button
          onClick={() => onSort("risk-asc")} // Ordena por risco, do menor para o maior.
          className={styles.sortButton}
          aria-label="Ordenar animais por nvel de risco de extino, do menor para o maior risco"
        >
          Risco (Menor)
        </button>
        <button
          onClick={() => onSort("risk-desc")} // Ordena por risco, do maior para o menor.
          className={styles.sortButton}
          aria-label="Ordenar animais por nvel de risco de extino, do maior para o menor risco"
        >
          Risco (Maior)
        </button>
        <button
          onClick={() => onSort("codigo-asc")} // Ordena por nmero do tazo (cdigo), do menor para o maior.
          className={styles.sortButton}
          aria-label="Ordenar animais por nmero do tazo (cdigo) em ordem crescente"
        >
          Tazo N (Crescente)
        </button>
        <button
          onClick={() => onSort("codigo-desc")} // Ordena por nmero do tazo (cdigo), do maior para o menor.
          className={styles.sortButton}
          aria-label="Ordenar animais por nmero do tazo (cdigo) em ordem decrescente"
        >
          Tazo N (Decrescente)
        </button>
      </div>{" "}
      {/* Fim de .sortControls */}
    </header> // Fim do componente Header
  );
}; // Fim da definio do componente Header

// --- 3. EXPORTAO DO COMPONENTE ---
// Torna o componente Header disponvel para ser usado em outros arquivos (principalmente no App.js).
export default Header;
