// Caminho: src/components/AnimalCard.jsx

// =================================================================================================
// ARQUIVO: AnimalCard.jsx
// PROPSITO: Este arquivo define um componente React chamado 'AnimalCard'.
//            Um "componente" em React  como um bloco de construo para a sua pgina da web.
//            Este componente especfico  projetado para exibir um "card" (carto) com
//            informaes resumidas sobre um nico animal. Ele tambm ter um efeito visual
//            interativo: o card vai "virar" (como um tazo ou uma carta de baralho)
//            quando o usurio interagir com ele (passando o mouse ou usando o teclado).
//
// CONCEITOS FUNDAMENTAIS DE REACT E JAVASCRIPT DEMONSTRADOS AQUI:
//  1. Componentes Funcionais: A forma moderna de criar componentes em React, usando funes JavaScript.
//  2. JSX (JavaScript XML): Uma extenso de sintaxe para JavaScript que se parece com HTML.
//                            usada para descrever como a interface do usurio deve parecer.
//  3. Props (Propriedades): Como os componentes recebem dados de seus "componentes pais".
//                           Este AnimalCard receber:
//                             - 'animal': Um objeto contendo todas as informaes do animal.
//                             - 'onSelectAnimal': Uma funo que ser chamada quando o card for clicado.
//  4. Estado (State) com o Hook 'useState':
//                           Como os componentes podem "lembrar" de informaes que podem mudar
//                           ao longo do tempo (neste caso, se o card est virado ou no).
//                           Quando o estado muda, o React automaticamente atualiza a aparncia do componente.
//  5. Manipuladores de Eventos (Event Handlers):
//                           Funes que so executadas em resposta a aes do usurio, como:
//                             - 'onClick': Quando o usurio clica no card.
//                             - 'onMouseEnter'/'onMouseLeave': Quando o cursor do mouse entra/sai da rea do card.
//                             - 'onFocus'/'onBlur': Quando o card recebe/perde foco via teclado.
//                             - 'onKeyDown': Para interaes com o teclado (como pressionar Enter ou Espao).
//  6. CSS Modules: Uma tcnica para escrever CSS de forma que os estilos sejam "escopados"
//                  localmente para este componente, evitando conflitos com estilos de outros
//                  componentes na aplicao.
//  7. Acessibilidade (a11y): Prticas para tornar o componente utilizvel por mais pessoas,
//                            incluindo aquelas que usam leitores de tela ou navegam pelo teclado.
//                            (ex: 'tabIndex', 'role', 'aria-label', 'alt' em imagens).
//  8. Renderizao Condicional (implcita): Como o que  exibido pode mudar com base no estado
//                                         (ex: mostrar a frente ou o verso do card).
// =================================================================================================

// --- 1. IMPORTAES DE MDULOS E FUNES NECESSRIAS ---

// Importa a biblioteca 'React' e o Hook 'useState'.
// - 'React':  a biblioteca principal que nos permite definir componentes e usar JSX.
//            O JSX que escrevemos (ex: <div>)  transformado em chamadas como React.createElement('div').
// - 'useState':  um "Hook" do React. Hooks so funes especiais que "engancham"
//              nos recursos internos do React, como o sistema de estado e ciclo de vida,
//              permitindo que os usemos em componentes funcionais.
//              'useState' especificamente nos permite adicionar uma varivel de "estado"
//              ao nosso componente.
import React, { useState } from "react";

// Importa o objeto 'styles' do nosso arquivo CSS Module associado ('AnimalCard.module.css').
// - 'styles': Este nome  uma conveno. Quando o arquivo CSS  nomeado com '.module.css',
//             o sistema de build do React (ou uma ferramenta como o Webpack) processa este CSS
//             de uma forma especial. Todas as classes CSS definidas nesse arquivo
//             (ex: a classe '.animalCard' em AnimalCard.module.css)
//             se tornam propriedades do objeto 'styles' (ex: styles.animalCard).
//             O valor dessas propriedades ser uma string com um nome de classe nico
//             gerado automaticamente (ex: "AnimalCard_animalCard__XyZ123").
//             Isso garante que os estilos sejam aplicados APENAS a este componente,
//             evitando conflitos de nomes de classes CSS com outros componentes.
import styles from "./AnimalCard.module.css";

// Importa funes utilitrias do nosso arquivo de dados ('animalData.js').
// - 'get(objeto, caminho, valorPadrao)': Uma funo que criamos para buscar valores
//                                       dentro de objetos JavaScript de forma segura.
//                                       Se tentarmos acessar `animal.prop.subprop` e `prop`
//                                       no existir, o JavaScript normalmente daria um erro.
//                                       A funo `get` nos ajuda a evitar isso, retornando
//                                       um 'valorPadrao' (como "N/A") se o 'caminho'
//                                       para a propriedade no for encontrado.
// - 'getExtinctionHexColor(categoria)': Retorna uma string com o cdigo hexadecimal
//                                      de uma cor (ex: "#FF0000" para vermelho)
//                                      baseada na categoria de extino do animal (ex: "EN" para Em Perigo).
import { get, getExtinctionHexColor } from "../data/animalData";

// --- 2. DEFINIO DO COMPONENTE FUNCIONAL 'AnimalCard' ---

// 'AnimalCard'  um componente funcional. Ele  definido como uma funo JavaScript.
// Esta funo aceita um nico argumento: um objeto chamado 'props' (abreviao de propriedades).
// Usamos a "desestruturao de objetos" do JavaScript na lista de parmetros da funo:
//   `({ animal, onSelectAnimal })`
// Isso  um atalho para pegar diretamente as propriedades 'animal' e 'onSelectAnimal'
// de dentro do objeto 'props' que o componente pai (provavelmente AnimalList.jsx) passar.
// Seria o mesmo que escrever:
//   const AnimalCard = (props) => {
//     const animal = props.animal;
//     const onSelectAnimal = props.onSelectAnimal;
//     // ... resto do cdigo do componente
//   }
const AnimalCard = ({ animal, onSelectAnimal }) => {
  // --- A. EXTRAO E PREPARAO DOS DADOS DO ANIMAL QUE SERO EXIBIDOS ---
  // Usamos a funo 'get' importada para buscar as informaes especficas do objeto 'animal'
  // que este card precisa exibir. Se uma informao no for encontrada nos dados do animal,
  // 'get' retornar o valor padro que especificamos.

  // Nome comum do animal, como "Jaguatirica". Usado como ttulo principal do card.
  const nomeComum = get(animal, "nome_tazo", "Nome Indisponvel");

  // Nome cientfico do animal, como "Leopardus pardalis". Exibido abaixo do nome comum.
  const nomeCientifico = get(
    animal,
    "nome_cientifico",
    "Nome Cientfico Indisponvel"
  );

  // URL da imagem para a FRENTE do "tazo" (o lado que aparece inicialmente).
  const imagemFrontal = get(
    animal, // O objeto do animal.
    "imagens.front", // O "caminho" para a propriedade: animal.imagens.front
    "https://via.placeholder.com/200x250/cccccc/888888?text=Frente+Indisponvel" // URL de uma imagem genrica de fallback.
  );

  // URL da imagem para o VERSO do "tazo" (o lado que aparece quando o card vira).
  const imagemVerso = get(
    animal,
    "imagens.back",
    "https://via.placeholder.com/200x250/aaaaaa/ffffff?text=Verso+Indisponvel"
  );

  // Informaes sobre o nvel de extino do animal.
  const categoriaExtincao = get(animal, "nivel_extincao.categoria", "N/A"); // Sigla, ex: "LC", "EN", "CR".
  const descricaoExtincao = get(
    animal,
    "nivel_extincao.descricao",
    "Status No Avaliado"
  ); // Descrio textual, ex: "Pouco Preocupante".

  // Obtm a cor de fundo (em formato hexadecimal, ex: "#AED581") para a etiqueta do status de extino.
  // Esta cor ser aplicada usando um estilo inline no JSX.
  const corDeFundoParaStatus = getExtinctionHexColor(categoriaExtincao);

  // --- B. GERENCIAMENTO DO ESTADO INTERNO DO CARD (se est virado ou no) ---
  // "Estado" (state) em React refere-se a dados que um componente mantm e que podem mudar
  // ao longo do tempo, geralmente em resposta a interaes do usurio ou outros eventos.
  // Quando o estado de um componente muda, o React automaticamente "re-renderiza"
  // (atualiza) esse componente na tela para refletir a nova informao.

  // Usamos o Hook 'useState' para criar uma varivel de estado chamada 'estaVirado'.
  // - `estaVirado`: Esta varivel booleana (true/false) vai "lembrar" se o card
  //                 est mostrando sua face do verso (true) ou da frente (false).
  // - `definirEstaVirado`: Esta  a FUNO especial que DEVEMOS usar para ATUALIZAR
  //                        o valor de 'estaVirado'. NUNCA modifique 'estaVirado'
  //                        diretamente (ex: `estaVirado = true;` NO FUNCIONA para o React).
  //                        Chamar `definirEstaVirado(novoValor)` informa ao React que o estado mudou
  //                        e que uma re-renderizao pode ser necessria.
  // - `useState(false)`: O valor `false` passado para `useState`  o VALOR INICIAL
  //                      da varivel de estado 'estaVirado'. Isso significa que, quando
  //                      o card  exibido pela primeira vez, ele estar mostrando a face frontal.
  const [estaVirado, definirEstaVirado] = useState(false);

  // --- C. FUNES PARA LIDAR COM INTERAES DO USURIO (MANIPULADORES DE EVENTOS / EVENT HANDLERS) ---
  // Estas so funes que sero chamadas quando certos eventos ocorrerem no card
  // (como o mouse passando sobre ele, ou o card recebendo foco do teclado).

  // Funo para VIRAR o card (mudar o estado 'estaVirado' para true).
  // Esta funo ser chamada quando o mouse ENTRAR na rea do card ('onMouseEnter')
  // ou quando o card receber FOCO do teclado ('onFocus').
  const lidarComEntradaDoMouseOuFocoNoCard = () => {
    // console.log(`Mouse/Foco ENTROU em: ${nomeComum}`); // Para depurao
    definirEstaVirado(true); // Atualiza o estado para 'true', fazendo o card virar.
  };

  // Funo para DESVIRAR o card (mudar o estado 'estaVirado' para false).
  // Esta funo ser chamada quando o mouse SAIR da rea do card ('onMouseLeave')
  // ou quando o card PERDER o FOCO do teclado ('onBlur').
  const lidarComSaidaDoMouseOuPerdaDeFocoDoCard = () => {
    // console.log(`Mouse/Foco SAIU de: ${nomeComum}`); // Para depurao
    definirEstaVirado(false); // Atualiza o estado para 'false', fazendo o card voltar  frente.
  };

  // Funo para lidar com o CLIQUE no card.
  // Esta funo ser chamada quando o usurio clicar no card inteiro ('onClick').
  // Seu propsito principal  informar ao componente pai (App.js) que este animal
  // foi selecionado, para que o App.js possa, por exemplo, mostrar a pgina de detalhes.
  const lidarComCliquePrincipalNoCard = () => {
    // Primeiro, verificamos se a funo 'onSelectAnimal' foi realmente passada como prop pelo App.js.
    //  uma boa prtica verificar se callbacks opcionais existem antes de cham-los.
    if (onSelectAnimal) {
      // Se 'onSelectAnimal' existe, ns a chamamos, passando o ID nico do animal.
      // Usamos 'get' para buscar 'id_animal' ou, como fallback, 'codigo'.
      const idUnicoDoAnimal = get(animal, "id_animal", get(animal, "codigo"));
      onSelectAnimal(idUnicoDoAnimal); // O App.js receber este ID e saber qual animal mostrar.
    } else {
      // Se, por algum motivo, 'onSelectAnimal' no foi fornecida, apenas registramos no console.
      console.warn(
        `AnimalCard: A funo 'onSelectAnimal' no foi fornecida para o animal "${nomeComum}". O clique no ter efeito de navegao.`
      );
    }
  };

  // --- D. ESTRUTURA JSX: O QUE O COMPONENTE 'AnimalCard' VAI RENDERIZAR NA TELA ---
  // A instruo 'return' define a "aparncia" do componente usando JSX.
  // JSX se parece muito com HTML, mas  na verdade JavaScript que ser transformado
  // em chamadas `React.createElement(...)` pelo Babel (uma ferramenta de compilao).
  return (
    // Usamos a tag <article> do HTML5. Semanticamente, um "card" com informaes
    // autocontidas sobre um item (como um animal) se encaixa bem na definio de <article>.
    <article
      // className: Em JSX, usamos 'className' em vez de 'class' (que  uma palavra reservada em JavaScript)
      //            para aplicar classes CSS. '{styles.animalCard}' pega a classe 'animalCard'
      //            do nosso arquivo CSS Module (AnimalCard.module.css) e a aplica aqui.
      //            O nome da classe no HTML final ser algo como "AnimalCard_animalCard__123Abc".
      className={styles.animalCard}
      // Manipuladores de Eventos para interatividade:
      onClick={lidarComCliquePrincipalNoCard} // Quando o card  clicado com o mouse.
      onMouseEnter={lidarComEntradaDoMouseOuFocoNoCard} // Quando o cursor do mouse entra na rea do card.
      onMouseLeave={lidarComSaidaDoMouseOuPerdaDeFocoDoCard} // Quando o cursor do mouse sai da rea do card.
      onFocus={lidarComEntradaDoMouseOuFocoNoCard} // Quando o card recebe foco (ex: via navegao por Teclado com a tecla Tab).
      onBlur={lidarComSaidaDoMouseOuPerdaDeFocoDoCard} // Quando o card perde o foco.
      // Manipulador de Evento para acessibilidade via teclado:
      // Permite que usurios que navegam com o teclado "cliquem" no card
      // pressionando as teclas "Enter" ou "Espao" quando o card est focado.
      onKeyDown={(eventoDoTeclado) => {
        // 'eventoDoTeclado'  um objeto com informaes sobre o evento de teclado.
        // 'eventoDoTeclado.key' contm o nome da tecla pressionada (ex: "Enter", " ", "a", "Shift").
        if (eventoDoTeclado.key === "Enter" || eventoDoTeclado.key === " ") {
          // 'eventoDoTeclado.preventDefault();' impede o comportamento padro do navegador
          // para estas teclas. Por exemplo, a tecla Espao normalmente rola a pgina para baixo.
          // Queremos que ela apenas "clique" no card aqui.
          eventoDoTeclado.preventDefault();
          lidarComCliquePrincipalNoCard(); // Chama a mesma funo do onClick.
        }
      }}
      // Atributos de Acessibilidade (a11y):
      tabIndex="0" // Torna o <article> "focvel" via teclado (tecla Tab).
      // Elementos como <div> e <article> no so focveis por padro.
      // '0' significa que ele entra na ordem natural de tabulao da pgina.
      role="button" // Informa aos leitores de tela que este <article> se comporta como um boto,
      // j que ele tem uma ao de clique.
      aria-label={`Ver detalhes sobre o animal: ${nomeComum}`} // Fornece uma descrio textual concisa
      // do propsito do elemento para leitores de tela.
      aria-pressed={estaVirado} // Indica para leitores de tela se o card est no estado "virado" (pressionado).
      // til para botes de alternncia (toggle buttons).
    >
      {/*
        Container Interno para o Efeito de Flip (o "Tazo" que vira):
        - Este <div> (com a classe 'styles.cardInterno')  o elemento que de fato vai girar.
        - Sua classe CSS  definida dinamicamente com base no estado 'estaVirado':
          - Sempre ter a classe base 'styles.cardInterno'.
          - Se 'estaVirado' for true, a classe 'styles.cardInternoVirado' tambm ser adicionada.
            (Ex: className="AnimalCard_cardInterno__abc AnimalCard_cardInternoVirado__def")
          - Se 'estaVirado' for false, apenas 'styles.cardInterno' ser aplicada.
          - A classe 'styles.cardInternoVirado' (definida no CSS) contm a transformao (`transform: rotateY(180deg);`)
            que faz o elemento girar.
        - 'aria-hidden="true"': Como o contedo visual do flip (as duas faces da imagem)  principalmente
          decorativo em relao  ao principal do card (que  o clique para ver detalhes),
          e as informaes textuais importantes (nome, status) esto visveis de qualquer forma,
          podemos marcar este container interno como "escondido" para tecnologias assistivas.
          Isso evita que leitores de tela tentem descrever a animao de flip de forma confusa.
      */}
      <div
        className={`${styles.cardInterno} ${
          estaVirado ? styles.cardInternoVirado : ""
        }`}
        aria-hidden="true"
      >
        {/* Face Frontal do Tazo: Contm a imagem da frente. */}
        <div className={`${styles.cardFace} ${styles.cardFrente}`}>
          <img
            src={imagemFrontal} // URL da imagem da frente.
            // O 'alt' text aqui pode ser vazio ('alt=""') porque a imagem  considerada decorativa
            // DENTRO do contexto do card como um todo, que j tem um 'aria-label' abrangente.
            // Se esta imagem fosse a NICA forma de identificar o animal, o 'alt' precisaria ser descritivo.
            // Para tazos, a arte  parte da apresentao, mas a informao principal vem do texto.
            alt=""
            className={styles.cardImagem} // Classe para estilizar a imagem (ex: tamanho, object-fit).
            loading="lazy" // "Lazy loading": instrui o navegador a carregar esta imagem
            // apenas quando ela estiver perto de se tornar visvel na tela.
            // Melhora o desempenho inicial da pgina, especialmente com muitas imagens.
            onError={(eventoDaImagem) => {
              // Esta funo  chamada se ocorrer um erro ao carregar a imagem (ex: URL quebrada, imagem no encontrada).
              // 'eventoDaImagem.target' se refere  tag <img> que falhou ao carregar.
              // Trocamos o 'src' para uma imagem placeholder e atualizamos o 'alt' para indicar o erro.
              eventoDaImagem.target.src =
                "https://via.placeholder.com/200x250/EEEEEE/757575?text=Erro+Frente";
              eventoDaImagem.target.alt =
                "Erro ao carregar imagem frontal do tazo";
            }}
          />
        </div>

        {/* Face do Verso do Tazo: Contm a imagem do verso. */}
        <div className={`${styles.cardFace} ${styles.cardVerso}`}>
          <img
            src={imagemVerso}
            alt="" // Decorativo, como a imagem frontal.
            className={styles.cardImagem}
            loading="lazy"
            onError={(eventoDaImagem) => {
              eventoDaImagem.target.src =
                "https://via.placeholder.com/200x250/EEEEEE/757575?text=Erro+Verso";
              eventoDaImagem.target.alt =
                "Erro ao carregar imagem do verso do tazo";
            }}
          />
        </div>
      </div>{" "}
      {/* Fim do div .cardInterno (o tazo que vira) */}
      {/*
        Container do Contedo de Texto:
        Esta parte do card fica ABAIXO do tazo que vira e permanece sempre visvel.
        Contm o nome do animal e seu status de extino.
      */}
      <div className={styles.cardConteudo}>
        {/* Agrupador para os nomes (comum e cientfico).
            til se precisarmos aplicar algum estilo especfico a este bloco de texto. */}
        <div>
          <h3 className={styles.animalNome}>{nomeComum}</h3>
          <p className={styles.nomeCientifico}>
            {/* A tag <em> (emphasis)  usada para dar nfase ao texto,
                que geralmente  renderizada como itlico pelos navegadores.
                 semanticamente apropriada para nomes cientficos. */}
            <em>{nomeCientifico}</em>
          </p>
        </div>

        {/*
          "Etiqueta" (Badge) para o Status de Extino:
          - className: Aplica o estilo base para a etiqueta.
          - style: A propriedade 'style' em JSX aceita um objeto JavaScript.
                   As chaves do objeto so nomes de propriedades CSS em camelCase
                   (ex: 'backgroundColor' em vez de 'background-color').
                   Os valores so strings.
                   Aqui, estamos definindo a cor de fundo da etiqueta dinamicamente,
                   com base na 'corDeFundoParaStatus' que calculamos anteriormente.
                   Ex: style={{ backgroundColor: '#AED581' }}
        */}
        <div
          className={styles.statusExtincao}
          style={{ backgroundColor: corDeFundoParaStatus }}
        >
          {/* Exibe a sigla e a descrio do status. Ex: "LC - Pouco Preocupante" */}
          {categoriaExtincao} - {descricaoExtincao}
        </div>
      </div>{" "}
      {/* Fim do div .cardConteudo */}
    </article> // Fim do <article> do AnimalCard
  );
}; // Fim da definio do componente AnimalCard

// --- 3. EXPORTAO DO COMPONENTE ---
// 'export default AnimalCard;' torna o componente 'AnimalCard' disponvel para
// ser importado e utilizado em outros arquivos/componentes da nossa aplicao
// (como no 'AnimalList.jsx' ou 'App.js').
// A palavra-chave 'default' significa que, quando outro arquivo importa deste (AnimalCard.jsx)
// sem usar chaves {}, ele receber este componente AnimalCard como o item principal exportado.
// Exemplo de importao em outro arquivo:
//   import QualquerNomeQueEuQuiser from './AnimalCard';
//   <QualquerNomeQueEuQuiser animal={...} onSelectAnimal={...} />
export default AnimalCard;
